import React from 'react'
import { HorizontalTabComponent } from '../../components/HorizontalTabComponent'

import { ArmorTabView } from './Tabs/ArmorTabView'
import { ItemTabView } from './Tabs/ItemTabView'
import { WeaponTabView } from './Tabs/WeaponTabView'

export function InventoryCheatInterface () {
  return (
    <HorizontalTabComponent
      tabs={[
        {
          title: 'Armors',
          baseId: 'armor-tab'
        },
        {
          title: 'Items',
          baseId: 'items-tab'
        },
        {
          title: 'Weapons',
          baseId: 'weapons-tab'
        }
      ]}>
      <ArmorTabView />
      <ItemTabView />
      <WeaponTabView />
    </HorizontalTabComponent>
  )
}
