import { IEnemyManager } from '../../sdk/lib/IEnemyManager'
import { IInGameEnemy } from '../../sdk/lib/data/IInGameEnemy'
import { InGameEnemy } from '../data/InGameEnemy'

export class EnemyManager implements IEnemyManager {
  private readonly _enemies: Map<number, IInGameEnemy>

  constructor () {
    const enemies = new Map<number, IInGameEnemy>()

    const troops = $gameTroop.members()
    for (let i = 0, l = troops.length; i < l; i++) {
      if (!troops[i]) continue
      enemies.set(i, new InGameEnemy(troops[i]))
    }

    this._enemies = enemies
  }

  get enemies (): IInGameEnemy[] {
    return [...this._enemies.keys()]
      .map(x => this._enemies.get(x) as IInGameEnemy)
  }

  getEnemy (id: number): IInGameEnemy | undefined {
    if (!this._enemies.has(id)) return
    return this._enemies.get(id)
  }

  killAll (): void {
    for (const enemy of this._enemies.keys()) {
      this._enemies.get(enemy)?.kill()
    }
  }

  restoreAll (): void {
    for (const enemy of this._enemies.keys()) {
      this._enemies.get(enemy)?.restore()
    }
  }

  singleHpAll (): void {
    for (const enemy of this._enemies.keys()) {
      this._enemies.get(enemy)?.singleHitHp()
    }
  }

  isOnBattle (): boolean {
    return $gameParty.inBattle()
  }
}
