import '@abraham/reflection'
import React from 'react'
import ReactDOM from 'react-dom/client'
import { ClientMain } from './ClientMain'
import { injectBuiltInPlugins } from './builtins/injectBuiltInPlugins'
import { injectBuiltInTranslators } from './translators/injectBuiltInTranslators'
import { getOrCreateRoot } from './utils/getOrCreateRoot'

import './scss/styles.scss'

import { LibraryBootstrapper } from './LibraryBootstrapper'
import { PluginManager } from './PluginManager'
import { IPlugin } from '../sdk/IPlugin'
import { ITranslator } from '../sdk/lib/ITranslator'
import { ExternalTranslatorManager } from './ExternalTranslatorManager'
import { ConfigurationManager } from './ConfigurationManager'
import { RuntimeStorage } from './utils/RuntimeStorage'

function setupWindowRelatedConfigs (config: ConfigurationManager) {
  window.require('nw.gui').Window.get().zoomLevel = config.read('zoom') ?? 1
}

function init (configManager: ConfigurationManager) {
  const pluginManager = new PluginManager()
  const libs = new LibraryBootstrapper()
  const translators = new ExternalTranslatorManager()
  const runtimeStorage = new RuntimeStorage()
  libs.registerLibraries()

  window.CheatUtilityPluginManager = {
    register (plugin: IPlugin) {
      pluginManager.registerPlugin(plugin)
    },
    registerTranslator: (providerName: string, providerInstance: ITranslator) => {
      translators.register(providerName, providerInstance)
    }
  }

  window.iLib = {
    require: function <T> (className: string) {
      return libs.getRequiredLibrary<T>(className)
    }
  }

  window.cheatConfigManager = {
    get: (name: string) => {
      return configManager.read(name)
    },
    set: (name: string, value: any) => {
      configManager.set(name, value)
    },
    getAll: () => {
      return configManager.readAll()
    }
  }

  window.runtimeMemory = {
    store: (name: string, value: any, override: boolean = false) => {
      runtimeStorage.store(name, value, override)
    },
    get: (name: string) => {
      return runtimeStorage.get(name)
    }
  }

  // Inject built-in plugins first
  setupWindowRelatedConfigs(configManager)
  injectBuiltInPlugins({ translatorManager: translators })
  injectBuiltInTranslators()

  // Inject plugins that are present in the plugins folder
  pluginManager.loadPluginsFromFolder()

  const root = ReactDOM.createRoot(getOrCreateRoot())
  root.render(<ClientMain pluginManager={pluginManager} />)
}

function isReady (): Promise<void> {
  return new Promise((resolve) => {
    const timer = setInterval(() => {
      if (!$dataActors || !$dataItems || !$dataWeapons || !$dataSystem || !$dataMapInfos) return
      if (typeof $gameParty === 'undefined' || !$gameParty) return
      if (typeof $gameActors === 'undefined' || !$gameActors) return
      if (typeof $gameVariables === 'undefined' || !$gameVariables) return
      if (typeof $gameMap === 'undefined' || !$gameMap) return
      if (typeof $gameTroop === 'undefined' || !$gameTroop) return

      clearInterval(timer)
      resolve()
    }, 100)
  })
}

// Paint the cheat tool when the page fully loads.
window.addEventListener('load', () => {
  isReady()
    .then(() => {
      const configManager = new ConfigurationManager()

      // Save the configuration before closing
      window.addEventListener('beforeunload', async () => {
        await configManager.save()
      })

      configManager.load()
        .then(() => init(configManager))
    })
    .catch(console.error)
})
