/* global IDataWeapon, IDataArmor, IDataItem */

import { ITranslationItem } from '../../sdk/lib/data/ITranslationBucket'

export function contractToTranslationItem (data: IDataWeapon[] | IDataArmor[] | IDataItem[]) {
  const contract: ITranslationItem[] = []

  for (let i = 0, l = data.length; i < l; i++) {
    if (!data[i] || !data[i]?.name || data[i]?.name?.startsWith('-')) continue
    contract.push({
      value: data[i].name,
      translated: ''
    })
  }

  return contract
}
