import { ReactElement } from 'react'
import { PluginTypes } from './enums/PluginTypes'
import { IPlugin } from './IPlugin'

export abstract class Plugin implements IPlugin {
  /**
   * Type of plugin
   */
  protected _pluginType: PluginTypes = PluginTypes.Cheat

  /**
   * Plugin ID. Must be unique. If the same plugin id already registered,
   * this won't be applied. It must not contain any invalid characters such as
   * spaces, asterisks or whatnot. Only allows underscores, dashes and letters
   * as valid IDs.
   */
  protected _pluginId: string = 'PluginName'

  /**
   * Name of the plugin
   */
  protected _name: string = 'Plugin name'

  /**
   * Author of the plugin
   */
  protected _author: string = 'Aiko Fujimoto'

  /**
   * Props to be passed into the component
   */
  protected _props: object = {}

  /**
   * Name of the plugin
   * @return {string}
   */
  getName (): string { return this._name }

  /**
   * ID of the plugin
   * @return {string}
   */
  getId (): string { return this._pluginId }

  /**
   * Plugin Type
   * @return {PluginTypes}
   */
  getPluginType (): PluginTypes { return this._pluginType }

  /**
   * Author of the Plugin
   * @return {string}
   */
  getAuthor (): string { return this._author }

  /**
   * Gets the icon of the element.
   */
  abstract getIcon(): ReactElement;

  setProp (prop: object) {
    this._props = prop;
  }

  /**
   * Gets the content of the plugin.
   */
  abstract getInterface (): ReactElement;
}
