import React, { Fragment, useState, useEffect } from 'react'
import { IMapSwitchesManager } from '../../../sdk/lib/IMapSwitchesManager'

interface MapSwitchesCheatState {
  [key: number]: {
    name: string;
    value: boolean;
  }
}

export function MapSwitchesCheatInterface () {
  const lib = window.iLib.require<IMapSwitchesManager>('MapSwitchesManager')
  const [switches, setSwitches] = useState<MapSwitchesCheatState>({})
  const [active, setActive] = useState<number>(-1)

  const updateAll = () => {
    setSwitches(() => {
      const switchData: MapSwitchesCheatState = {}

      for (const sw of lib.data) {
        switchData[sw.id] = {
          name: sw.name,
          value: sw.value
        }
      }

      return switchData
    })
  }

  // Run once
  useEffect(() => {
    updateAll()
  }, [])

  const handleSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value)) return
    setActive(value)
  }

  const toggleSwitch = () => {
    const newValue = !switches[active].value
    lib.update(active, newValue)

    updateAll()
  }

  const toggleAllEnable = () => {
    lib.enableAllSwitches()
    updateAll()
  }

  const toggleAllDisabled = () => {
    lib.disableAllSwitches()
    updateAll()
  }

  return (
    <Fragment>
      <button className="btn btn-danger btn-sm mb-1 w-100" onClick={toggleAllEnable}>Enable ALL switches on map</button>
      <button className="btn btn-danger btn-sm w-100" onClick={toggleAllDisabled}>Disable ALL switches on map</button>
      <hr className="w-100 mt-1 mb-1 d-block" />
      <select className="form-select form-select-sm w-100 mb-1" value={active} onChange={handleSelectionChange}>
        <option value="-1">Select switch</option>
        {
          Object.keys(switches)
            .map(x => Number(x))
            .map((x, index: number) => (
              <option key={index} value={x}>#{x}: {switches[x].name}</option>
            ))
        }
      </select>
      {
        active > -1 && (
          <button
            className={`btn ${switches[active].value ? 'btn-success' : 'btn-danger'} w-100 btn-sm`}
            onClick={toggleSwitch}>
            Toggle Switch
          </button>
        )
      }
    </Fragment>
  )
}
