import { PlayerCheatPlugin } from './PlayerCheatPlugin'
import { InventoryCheatPlugin } from './InventoryCheatPlugin'
import { PartyCheatPlugin } from './PartyCheatPlugin'
import { SwitchCheatPlugin } from './SwitchCheatPlugin'
import { VariablesCheatPlugin } from './VariablesCheatPlugin'
import { MapSwitchesCheatPlugin } from './MapSwitchesCheatPlugin'
import { MapCheatPlugin } from './MapCheatPlugin'
import { EnemyCheatPlugin } from './EnemyCheatPlugin'
import { TranslatorPlugin } from './TranslatorPlugin'
import { ConfigurationPage } from './ConfigurationPage'
import { ExternalTranslatorManager } from '../ExternalTranslatorManager'

interface PluginDependency {
  translatorManager: ExternalTranslatorManager
}

/**
 * Built-in cheat utility plugins
 */
export function injectBuiltInPlugins (deps: PluginDependency) {
  window.CheatUtilityPluginManager.register(new PlayerCheatPlugin())
  window.CheatUtilityPluginManager.register(new PartyCheatPlugin())
  window.CheatUtilityPluginManager.register(new InventoryCheatPlugin())
  window.CheatUtilityPluginManager.register(new SwitchCheatPlugin())
  window.CheatUtilityPluginManager.register(new VariablesCheatPlugin())
  window.CheatUtilityPluginManager.register(new MapSwitchesCheatPlugin())
  window.CheatUtilityPluginManager.register(new MapCheatPlugin())
  window.CheatUtilityPluginManager.register(new EnemyCheatPlugin())

  const translator = new TranslatorPlugin()
  translator.setProp({ translatorManager: deps.translatorManager })
  window.CheatUtilityPluginManager.register(translator)

  window.CheatUtilityPluginManager.register(new ConfigurationPage())
}
