import React, { Fragment, useEffect, useState } from 'react'
import { IEnemyManager } from '../../../sdk/lib/IEnemyManager'
import { IInternalTimerCollection } from '../../../sdk/lib/IInternalTimerCollection'

interface InternalEnemyCheatInterfaceState {
  onBattle: boolean;
  autoKillActive: boolean;
}

interface InGameBattler {
  id: number;
  name: string;
  hp: number;
}

interface BattlerState {
  [id: number]: InGameBattler
}

export function EnemyCheatInterface () {
  const lib = window.iLib.require<IEnemyManager>('EnemyManager')
  const timers = window.iLib.require<IInternalTimerCollection>('InternalTimers')
  const [internalStates, setInternalStates] = useState<InternalEnemyCheatInterfaceState>({
    onBattle: false,
    autoKillActive: false
  })
  const [battlers, setBattlers] = useState<BattlerState>({})
  const [active, setActive] = useState(-1)

  const fetchAllBattlers = () => {
    setBattlers(() => {
      const battlers: BattlerState = {}

      for (const battler of lib.enemies) {
        battlers[battler.id] = {
          id: battler.id,
          name: battler.name,
          hp: battler.hp
        }
      }

      return battlers
    })
  }

  useEffect(() => {
    setInternalStates({
      onBattle: lib.isOnBattle(),
      autoKillActive: timers.hasTimer('internal-enemy_manager-auto-kill')
    })

    if (lib.isOnBattle()) {
      fetchAllBattlers()
    }
  }, [])

  const toggleAutoKill = () => {
    if (internalStates.autoKillActive) {
      timers.clearTimer('internal-enemy_manager-auto-kill')
      setInternalStates(lastStates => ({
        ...lastStates,
        autoKillActive: false
      }))

      return
    }

    if (!internalStates.autoKillActive) {
      timers.registerTimer('internal-enemy_manager-auto-kill', setInterval(() => {
        const _lib = window.iLib.require<IEnemyManager>('EnemyManager')
        if (_lib.isOnBattle()) {
          _lib.killAll()
        }
      }, 150))
      setInternalStates(lastStates => ({
        ...lastStates,
        autoKillActive: true
      }))
    }
  }

  const killAll = () => {
    lib.killAll()
    setInternalStates(lastStates => ({
      ...lastStates,
      onBattle: false
    }))
  }

  const setAllHPTo1 = () => {
    lib.singleHpAll()
    fetchAllBattlers()
  }

  const restoreHP = () => {
    lib.restoreAll()
    fetchAllBattlers()
  }

  const handleBattlerSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    const id = Number(event.currentTarget.value)

    if (!Number.isInteger(id)) return
    setActive(id)
  }

  const killSpecific = () => {
    lib.getEnemy(active)?.kill()
    fetchAllBattlers()
  }

  const restoreSpecific = () => {
    lib.getEnemy(active)?.restore()
    fetchAllBattlers()
  }

  const setHPTo1Specific = () => {
    lib.getEnemy(active)?.restore()
    fetchAllBattlers()
  }

  return (
    <Fragment>
      <button
        className={`btn btn-sm w-100 mb-1 ${internalStates.autoKillActive ? 'btn-danger' : 'btn-info'}`}
        onClick={toggleAutoKill}>
        {internalStates.autoKillActive ? 'Disable' : 'Enable'} auto kill
      </button>
      <hr className="w-100 d-block mb-1" />
      {
        internalStates.onBattle
          ? (
              <Fragment>
                <button className="btn btn-sm w-100 btn-danger mb-1" onClick={killAll}>Kill all enemies</button>
                <button className="btn btn-sm w-100 btn-info mb-1" onClick={setAllHPTo1}>Set all enemies HP to 1</button>
                <button className="btn btn-sm w-100 btn-success mb-1" onClick={restoreHP}>Restore HP</button>
                <hr className="w-100 d-block mb-1" />
                <select className="form-select form-select-sm w-100" value={active} onChange={handleBattlerSelectionChange}>
                  <option value="-1">Select Battler</option>
                  {
                    Object.keys(battlers).map((x, index) => (
                      <option key={index} value={battlers[Number(x)].id}>#{battlers[Number(x)].id} ${battlers[Number(x)].name}</option>
                    ))
                  }
                </select>
                {
                  active >= 0 && (
                    <Fragment>
                      <button
                        className="btn btn-danger btn-sm w-100 mt-1"
                        onClick={killSpecific}>
                        Kill
                      </button>
                      <button
                        className="btn btn-info btn-sm w-100 mt-1"
                        onClick={setHPTo1Specific}>
                        Set HP to 1
                      </button>
                      <button
                        className="btn btn-success btn-sm w-100 mt-1"
                        onClick={restoreSpecific}>
                        Restore
                      </button>
                    </Fragment>
                  )
                }
              </Fragment>
            )
          : (<p>Player is not in battle.</p>)
      }
    </Fragment>
  )
}
