import { IInventoryManager } from '../../sdk/lib/IInventoryManager'
import { InventoryData } from '../../sdk/lib/data/InventoryData'
import { IInventoryInformation } from '../../sdk/lib/data/IInventoryInformation'

export abstract class InventoryManager implements IInventoryManager {
  protected _inventoryData: InventoryData[] = []

  getAll (count: number = 1): void {
    // Ensure that we default to 1 as always, regardless what value is passed in.
    count |= 1

    // Maximum amount is capped at 99.
    if (count > 99) count = 99

    for (const data of this._inventoryData) {
      if (!data || !data.name) continue
      this.set(data.id, count)
    }
  }

  protected abstract queryCount(itemId: number): number;

  get items (): IInventoryInformation[] {
    return this._inventoryData
      .map(i => ({
        name: i.name,
        id: i.id,
        count: this.queryCount(i.id)
      }))
  }

  removeAll (): void {
    for (const data of this._inventoryData) {
      this.set(data.id, 0)
    }
  }

  abstract set(itemId: number, count: number): void;

  getItemInformation (itemId: number): IInventoryInformation | null {
    const data = this._inventoryData.filter(x => x.id === itemId)
    if (data.length) {
      return {
        id: data[0].id,
        name: data[0].name,
        count: this.queryCount(data[0].id)
      }
    }

    return null
  }
}
