import React from 'react'
import { ToolsIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'
import { ConfigurationPageInterface } from './ConfigurationPageInterface'

export class ConfigurationPage extends Plugin<{}> {
  constructor () {
    super()
    this._pluginType = PluginTypes.Cheat
    this._pluginId = 'ConfigurationPage'
    this._author = 'Aiko Fujimoto'
    this._name = 'Configuration Page'
  }

  getIcon (): React.ReactElement {
    return (<ToolsIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<ConfigurationPageInterface />)
  }
}
