import { singleton } from 'tsyringe'
import { IBasicPlayerData } from '../../sdk/lib/data/IBasicPlayerData'
import { IPartyManager } from '../../sdk/lib/IPartyManager'

@singleton()
export class PartyManager implements IPartyManager {
  addGold (value: number): void {
    $gameParty?.gainGold(value)
  }

  get partyGold (): number {
    return $gameParty?._gold ?? 0
  }

  setGold (value: number): void {
    $gameParty._gold = value
  }

  addPlayerToParty (actorId: number): void {
    if (!$gameActors.actor(actorId)) return
    $gameParty?.addActor(actorId)
  }

  get addablePlayers (): IBasicPlayerData[] {
    return $dataActors
      .filter(x => x && x?.name)
      .map(x => ({
        id: x.id,
        name: x.name
      }))
  }

  removePlayerFromParty (actorId: number) {
    const party = $gameParty.members()
    if (party.length <= 1) return

    for (const member of party) {
      if (member.actorId() === actorId) {
        $gameParty.removeActor(member.actorId())
      }
    }
  }

  getPartyMembers (): IBasicPlayerData[] {
    return $gameParty.members()
      .filter(x => x)
      .map(x => ({
        id: x._actorId,
        name: x._name
      }))
  }

  toggleNoClip (value: boolean) {
    $gamePlayer._through = value
  }

  isNoClipActive (): boolean {
    return $gamePlayer._through
  }
}
