import React, { Fragment, useEffect, useState } from 'react'
import { IMapManager } from '../../../sdk/lib/IMapManager'
import { SmallInputComponent } from '../../components/forms/SmallInputComponent'

interface LocationStateBase {
  x: number;
  y: number;
}

interface LocationState extends LocationStateBase {
  id: number;
  name: string;
}

export function MapCheatInterface () {
  const lib = window.iLib.require<IMapManager>('MapManager')
  const [location, setLocation] = useState<LocationState>({
    id: 0,
    name: 'Not yet loaded',
    x: 0,
    y: 0
  })
  const [selectedMap, setSelectedMap] = useState<number>(-1)
  const [fieldLocation, setFieldLocation] = useState<LocationStateBase>({
    x: 0,
    y: 0
  })

  useEffect(() => {
    setInterval(() => {
      const location = lib.current
      setLocation(() => ({
        id: location.id,
        name: location.name,
        x: location.x,
        y: location.y
      }))
    }, 500)
  }, [])

  const handleMapSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value)) return
    setSelectedMap(value)
  }

  const handleCoordinateInput = (target: keyof LocationStateBase, newValue: string | number) => {
    const value = Number(newValue)

    if (!Number.isInteger(value)) return

    setFieldLocation(lastState => ({
      ...lastState,
      [target]: value
    }))
  }

  const handleTeleport = () => {
    lib.teleport(selectedMap, fieldLocation.x, fieldLocation.y)
  }

  return (
    <Fragment>
      <p className="fs-6 d-block w-100 mb-1">Location data:</p>
      <p className="fs-6 d-block w-100 mb-1">ID: {location.id}</p>
      <p className="fs-6 d-block w-100 mb-1">Name: {location.name}</p>
      <p className="fs-6 d-block w-100 mb-1">X Coordinate: {location.x}</p>
      <p className="fs-6 d-block w-100">Y Coordinate: {location.y}</p>
      <hr className="d-block w-100 mt-1 mb-1" />
      <p className="fs-6 d-block w-100 mb-1">Teleport</p>
      <select className="form-select form-select-sm w-100 mb-1" onChange={handleMapSelectionChange} value={selectedMap}>
        <option value="-1">Select map</option>
        {
          lib.list.map((x, index) => (
            <option key={index} value={x.id}>#{x.id} {x.name}</option>
          ))
        }
      </select>
      {
        selectedMap >= 1 && (
          <Fragment>
            <SmallInputComponent
              name="X Coordinate"
              baseId="map-selection-x-coordinate"
              inputType="number"
              value={fieldLocation.x}
              handleOnChange={(e) => handleCoordinateInput('x', e.currentTarget.value)}
              buttonFeatures={[]}
            />
            <SmallInputComponent
              name="Y Coordinate"
              baseId="map-selection-y-coordinate"
              inputType="number"
              value={fieldLocation.y}
              handleOnChange={(e) => handleCoordinateInput('y', e.currentTarget.value)}
              buttonFeatures={[]}
            />
            <button
              className="btn btn-success w-100 d-block"
              onClick={handleTeleport}>
              Teleport
            </button>
          </Fragment>
        )
      }
    </Fragment>
  )
}
