import React, { Fragment, useEffect, useState } from 'react'
import { IPlayerManager } from '../../../sdk/lib/IPlayerManager'
import { ButtonFeatures, SmallInputComponent } from '../../components/forms/SmallInputComponent'

interface PlayerInformation {
  name: string;
  hp: number;
  mp: number;
  tp: number;
  level: number;
  exp: number;
}

interface ExperienceData {
  [key: number]: number;
}

export function PlayerCheatInterface () {
  // Dependency
  const lib = window.iLib.require<IPlayerManager>('PlayerManager')

  const [active, setActive] = useState(-1)
  const [player, setPlayerInfo] = useState<PlayerInformation>({
    name: '',
    hp: 0,
    tp: 0,
    mp: 0,
    level: 0,
    exp: 0
  })

  const defaultPlayerInformation = () => {
    setPlayerInfo({
      name: '',
      hp: 0,
      tp: 0,
      mp: 0,
      level: 0,
      exp: 0
    })
  }

  const refreshPlayerInformation = () => {
    lib.setPlayer(active)
    const information = lib.character
    if (!information) return

    setPlayerInfo({
      name: information.name(),
      hp: information.hp,
      tp: information.tp,
      mp: information.mp,
      level: information.level ?? 0,
      exp: (information._exp as ExperienceData)[active] ?? 0
    })
  }

  useEffect(() => {
    if (active === -1 || !lib.isPlayerExist(active)) {
      lib.unsetPlayer()
      defaultPlayerInformation()

      return
    }

    lib.setPlayer(active)
    refreshPlayerInformation()
  }, [active])

  const handleChange = (event: React.SyntheticEvent<HTMLSelectElement>): void => {
    const id = Number(event.currentTarget.value)

    // Don't accept non-integer values
    if (!Number.isInteger(id)) return

    setActive(id)
  }

  const onTextFieldChange = (targetProperty: keyof PlayerInformation, value: string | number): void => {
    // Check for type
    const typedValue: string | number = typeof player[targetProperty] === 'string'
      ? value
      : Number(value)

    setPlayerInfo({
      ...player,
      [targetProperty]: typedValue
    })
  }

  const resetFieldAction = (targetProperty: keyof PlayerInformation): void => {
    // Get player information
    const selectedPlayer = lib.character

    // Reject changes if player is not available.
    if (!selectedPlayer) return

    switch (targetProperty) {
      case 'name':
        setPlayerInfo({ ...player, name: selectedPlayer._name })
        break
      case 'hp':
        setPlayerInfo({ ...player, hp: selectedPlayer.hp })
        break
      case 'mp':
        setPlayerInfo({ ...player, mp: selectedPlayer.mp })
        break
      case 'tp':
        setPlayerInfo({ ...player, tp: selectedPlayer.tp })
        break
      case 'exp':
        setPlayerInfo({ ...player, exp: (selectedPlayer._exp as ExperienceData)[active] ?? 0 })
        break
    }
  }

  const addFieldAction = (targetProperty: keyof PlayerInformation) => {
    const value = Number(player[targetProperty])

    // No changes to push when NaN.
    if (Number.isNaN(value)) return

    switch (targetProperty) {
      case 'hp':
        lib.addHp(value)
        break
      case 'mp':
        lib.addMp(value)
        break
      case 'tp':
        lib.addTp(value)
        break
      case 'exp':
        lib.addXp(value)
        break
    }

    refreshPlayerInformation()
  }

  const setFieldAction = (targetProperty: keyof PlayerInformation) => {
    let value: string | number = player[targetProperty]

    if (typeof player[targetProperty] === 'number') {
      value = Number(player[targetProperty])

      // No changes to push when NaN.
      if (!Number.isInteger(value)) return
    }

    switch (targetProperty) {
      case 'name':
        lib.setName(value.toString())
        break
      case 'hp':
        lib.setHp(Number(value))
        break
      case 'mp':
        lib.setMp(Number(value))
        break
      case 'tp':
        lib.setTp(Number(value))
        break
    }

    refreshPlayerInformation()
  }

  const maxAllAction = () => {
    lib.max()
    refreshPlayerInformation()
  }

  const getInputFeatures = (targetProperty: keyof PlayerInformation): ButtonFeatures[] => {
    switch (targetProperty) {
      case 'name':
        return ['set', 'reset']
      case 'hp':
      case 'mp':
      case 'tp':
        return ['add', 'set', 'reset']
      case 'exp':
        return ['add', 'reset']
    }

    return []
  }

  return (
    <Fragment>
      <label className="d-block w-100 fs-6">Select Player</label>
      <select
        className="form-select form-select-sm w-100"
        onChange={handleChange}
        value={active.toString()}
      >
        <option value="-1">Select a player</option>
        {lib.list().map((x, index) => (
          <option key={index} value={x.id}>
            #{x.id} {x.name}
          </option>
        ))}
      </select>
      {
        active > -1
          ? (
              <Fragment>
                <div className="w-100 d-block mt-1" />
                  {['name', 'hp', 'mp', 'tp', 'exp', 'level'].map((x, index) => (
                    <SmallInputComponent
                      key={index}
                      name={x}
                      baseId={x}
                      inputType={x === 'name' ? 'text' : 'number'}
                      value={player[x as keyof PlayerInformation]}
                      handleOnChange={(event) => {
                        onTextFieldChange(x as keyof PlayerInformation, event.currentTarget.value)
                      }}
                      readonly={x === 'level'}
                      buttonFeatures={getInputFeatures(x as keyof PlayerInformation)}
                      setButtonAction={() => setFieldAction(x as keyof PlayerInformation)}
                      addButtonAction={() => addFieldAction(x as keyof PlayerInformation)}
                      resetButtonAction={() => resetFieldAction(x as keyof PlayerInformation)}
                    />
                  ))}
                  <button
                    className="btn btn-success btn-sm mb-1 w-100"
                    onClick={maxAllAction}>
                      Max All Stats
                  </button>
                  <button
                    className="btn btn-primary btn-sm mb-1 w-100"
                    onClick={() => refreshPlayerInformation()}>
                      Refresh/Reset Information
                  </button>
              </Fragment>
            )
          : ''
      }
    </Fragment>
  )
}
