import React, { Fragment, useEffect, useState } from 'react'
import { IVariablesManager, VariableTypes } from '../../../sdk/lib/IVariablesManager'
import { SmallTypedInputComponent, AllowedFormTypes } from '../../components/forms/SmallTypedInputComponent'

interface VariablesInterfaceData {
  name: string;
  type: VariableTypes;
  value: string | number | null;
}

interface VariablesInterfaceRangeState {
  from: number;
  to: number;
  defaultType: VariableTypes;
  value: string | number | null;
}

interface VariablesInterfaceState {
  [key: number]: VariablesInterfaceData
}

export function VariablesCheatInterface () {
  const lib = window.iLib.require<IVariablesManager>('VariablesManager')
  const [data, setData] = useState<VariablesInterfaceState>({})
  const [global, setGlobal] = useState<VariablesInterfaceData>({
    name: 'fr',
    type: 'string',
    value: ''
  })
  const [range, setRange] = useState<VariablesInterfaceRangeState>({
    from: 0,
    to: 0,
    defaultType: 'string',
    value: ''
  })
  const [active, setActive] = useState<number>(-1)

  const refreshState = () => {
    setData(() => {
      const dataToMerge: VariablesInterfaceState = {}

      for (const variable of lib.list) {
        dataToMerge[variable.id] = {
          name: variable.key,
          type: variable.type,
          value: variable.type === 'null' ? null : variable.value
        }
      }

      return dataToMerge
    })
  }

  // Apply variables to initial state.
  useEffect(() => {
    refreshState()
  }, [])

  const handleSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value)) return
    setActive(value)
  }

  const mapFormTypes = (type: VariableTypes): AllowedFormTypes => {
    if (type === 'null' || type === 'string') return 'text'
    return 'number'
  }

  const mapValueType = (formType: VariableTypes, value: string | number): string | number => {
    if (formType === 'string') return value.toString()
    if (formType === 'number') {
      const numericalValue = Number(value)
      if (!Number.isInteger(numericalValue)) return 0
      return numericalValue
    }

    return ''
  }

  const defaultValueType = (formType: VariableTypes): string | number | null => {
    if (formType === 'string') return ''
    if (formType === 'number') return 0
    return null
  }

  const handleOnInputChange = (value: string | number) => {
    setData(lastState => ({
      ...lastState,
      [active]: {
        ...lastState[active],
        value: mapValueType(lastState[active].type, value)
      }
    }))
  }

  const handleDropdownSelect = (value: string) => {
    setData(lastState => ({
      ...lastState,
      [active]: {
        ...lastState[active],
        type: value as VariableTypes,
        value: defaultValueType(value as VariableTypes)
      }
    }))
  }

  const performNumConversion = (value: string): number => {
    const numericalValue = Number(value)
    if (Number.isNaN(numericalValue)) return 0

    return numericalValue
  }

  const handleRangeChange = (target: keyof VariablesInterfaceRangeState, value: string) => {
    setRange(last => ({
      ...last,
      [target]: performNumConversion(value)
    }))
  }

  const handleTypeSelection = (value: string) => {
    setRange(last => ({
      ...last,
      defaultType: value as VariableTypes,
      value: defaultValueType(value as VariableTypes)
    }))
  }

  const rangeValueHandleOnChange = (value: string | number) => {
    setRange(last => ({
      ...last,
      value: mapValueType(last.defaultType, value)
    }))
  }

  const globalHandleOnChange = (value: string | number) => {
    setGlobal(lastState => ({
      ...lastState,
      value: mapValueType(lastState.type, value)
    }))
  }

  const globalHandleDropdownSelect = (value: string) => {
    setGlobal(lastState => ({
      ...lastState,
      type: value as VariableTypes,
      value: defaultValueType(value as VariableTypes)
    }))
  }

  const applyRangeValues = () => {
    for (let i = range.from; i <= range.to; i++) {
      lib.modify(i, range.defaultType, range.value)
    }
  }

  const globalUpdate = () => {
    lib.fill(global.type, global.value)
    refreshState()
  }

  const handleSetAction = () => {
    lib.modify(active, data[active].type, data[active].value)
    refreshState()
  }

  return (
    <Fragment>
      <p className="fs-6 mb-1 w-100">Update all variables</p>
      <SmallTypedInputComponent
        baseId="selectionGlobal"
        name="Value:"
        type={mapFormTypes(global.type)}
        readonly={global.type === 'null'}
        value={global.value ?? 'null value'}
        handleOnChange={globalHandleOnChange}
        defaultDropdownValue={global.type}
        dropdownOptions={['string', 'number', 'null']}
        handleDropdownSelect={globalHandleDropdownSelect}
        handleSetAction={globalUpdate}
      />
      <hr className="w-100 d-block mt-1 mb-1" />
      <p className="fs-6 mb-1 w-100">Update Individual Variable:</p>
      <select className="form-select form-select-sm w-100 mb-1" value={active} onChange={handleSelectionChange}>
        <option value="-1">Select Variable</option>
        {
          Object.keys(data)
            .map(x => Number(x))
            .map((x, index) => (
              <option key={index} value={x}>#{x}: {data[x].name}</option>
            ))
        }
      </select>
      {
        active > -1 && (
          <SmallTypedInputComponent
            baseId={`selectionId-${active}`}
            name="Value:"
            type={mapFormTypes(data[active].type)}
            readonly={data[active].type === 'null'}
            value={data[active].value ?? 'null value'}
            handleOnChange={handleOnInputChange}
            defaultDropdownValue={data[active].type}
            dropdownOptions={['string', 'number', 'null']}
            handleDropdownSelect={handleDropdownSelect}
            handleSetAction={handleSetAction}
          />
        )
      }
      <hr className="w-100 d-block mt-1 mb-1" />
      <p className="fs-6 mb-1 w-100">Update Ranges:</p>
      <div className="input-group mb-2 w-100">
        <span className="input-group-text" id="from-label">from</span>
        <input type="number"
               className="form-control"
               placeholder="from"
               aria-label="from"
               aria-labelledby="from-label"
               value={range.from}
               onChange={e => handleRangeChange('from', e.target.value)}
        />
      </div>
      <div className="input-group mb-2 w-100">
        <span className="input-group-text" id="to-label">to</span>
        <input type="number"
               className="form-control"
               placeholder="to"
               aria-label="to"
               aria-labelledby="to-label"
               value={range.to}
               onChange={e => handleRangeChange('to', e.target.value)}
        />
      </div>
      <SmallTypedInputComponent
        baseId="selectionGlobal"
        name="Value:"
        type={mapFormTypes(range.defaultType)}
        readonly={range.defaultType === 'null'}
        value={range.value ?? 'null value'}
        handleOnChange={rangeValueHandleOnChange}
        defaultDropdownValue={range.defaultType}
        dropdownOptions={['string', 'number', 'null']}
        handleDropdownSelect={handleTypeSelection}
        handleSetAction={applyRangeValues}
      />
    </Fragment>
  )
}
