import { singleton } from 'tsyringe'
import { IInternalTimerCollection } from '../../sdk/lib/IInternalTimerCollection'

@singleton()
export class InternalTimerCollection implements IInternalTimerCollection {
  private readonly _timer: Map<string, ReturnType<typeof setInterval>>

  constructor () {
    this._timer = new Map<string, ReturnType<typeof setInterval>>()
  }

  private _clearTimer (name: string): void {
    clearInterval(this._timer.get(name))
    this._timer.delete(name)
  }

  clearTimer (name?: string): void {
    if (!name) {
      for (const timer of [...this._timer.keys()]) {
        this._clearTimer(timer)
      }
      return
    }
    if (!this._timer.has(name)) return
    this._clearTimer(name)
  }

  hasTimer (name: string): boolean {
    return this._timer.has(name)
  }

  registerTimer (name: string, timer: ReturnType<typeof setInterval>): void {
    if (this._timer.has(name)) return
    this._timer.set(name, timer)
  }
}
