import React, { Fragment, useState, useEffect } from 'react'
import { ISwitchManager } from '../../../sdk/lib/ISwitchManager'

interface SwitchInterfaceData {
  name: string;
  value: boolean;
}

interface SwitchCheatInterfaceState {
  [key: number]: SwitchInterfaceData
}

export function SwitchCheatInterface () {
  const [switches, setSwitches] = useState<SwitchCheatInterfaceState>({})
  const [active, setActive] = useState<number>(-1)
  const lib = window.iLib.require<ISwitchManager>('SwitchManager')

  const refreshState = () => {
    setSwitches(() => {
      const data: SwitchCheatInterfaceState = {}

      for (const sw of lib.list) {
        data[sw.id] = {
          name: sw.name,
          value: sw.value
        }
      }

      return data
    })
  }

  // Trigger on initial render
  useEffect(() => {
    refreshState()
  }, [])

  const toggleSwitch = (targetId: number) => {
    const id = Number(targetId)

    if (!Number.isInteger(id)) return
    const newValue = !lib.getSwitch(id)?.value
    lib.modify(id, newValue)

    refreshState()
  }

  const toggleEnableAll = () => {
    lib.enableAll()
    refreshState()
  }

  const toggleDisableAll = () => {
    lib.disableAll()
    refreshState()
  }

  const handleSwitchSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value)) return
    setActive(value)
  }

  return (
    <Fragment>
      <button className="btn btn-danger d-block w-100 btn-sm mb-1" onClick={toggleEnableAll}>Enable All</button>
      <button className="btn btn-danger d-block w-100 btn-sm mb-1" onClick={toggleDisableAll}>Disable All</button>
      <hr className="w-100 d-block mt-1 mb-1" />
      <select className="form-select form-select-sm w-100 mb-1" onChange={handleSwitchSelectionChange} value={active}>
        <option value="-1">Select Switch</option>
        {
          Object.keys(switches)
            .map(x => Number(x))
            .map((x, index) => (
              <option key={index} value={x}>#{x}: [{switches[x].value ? 'on' : 'off'}] {switches[x].name}</option>
            ))
        }
      </select>
      {
        active > -1 && (
          <button
            className={`btn ${switches[active].value ? 'btn-success' : 'btn-danger'} w-100 btn-sm`}
            onClick={() => toggleSwitch(active)}>
            Toggle Switch
          </button>
        )
      }
    </Fragment>
  )
}
