import React, { Fragment, useState, useEffect } from 'react'
import { SmallInputComponent } from '../../components/forms/SmallInputComponent'

interface ConfigData {
  [key: string]: any;
}

export function ConfigurationPageInterface () {
  const [config, setConfig] = useState<ConfigData>({})

  const update = () => {
    setConfig(window.cheatConfigManager.getAll())
  }

  useEffect(() => {
    update()
  }, [])

  const freezeOnOpenChange = () => {
    window.cheatConfigManager
      .set('freezeOnOpen', Boolean(!window.cheatConfigManager.get('freezeOnOpen')))
    update()
  }

  const updateZoomSize = (e: React.SyntheticEvent<HTMLInputElement>) => {
    const newValue = Number(e.currentTarget.value)
    if (newValue < 0 || newValue > 3) return

    window.require('nw.gui').Window.get().zoomLevel = newValue
    window.cheatConfigManager.set('zoom', newValue)
    update()
  }

  const updateApiKey = (e: React.SyntheticEvent<HTMLInputElement>) => {
    setConfig({
      ...config,
      azureTranslatorApiKey: e.currentTarget.value
    })
  }

  const submitApiKey = () => {
    window.cheatConfigManager.set('azureTranslatorApiKey', config.azureTranslatorApiKey)
    update()
  }

  const updateEndpoint = (e: React.SyntheticEvent<HTMLInputElement>) => {
    setConfig({
      ...config,
      azureTranslatorEndpoint: e.currentTarget.value
    })
  }

  const submitEndpoint = () => {
    window.cheatConfigManager.set('azureTranslatorEndpoint', config.azureTranslatorEndpoint)
    update()
  }

  const updateLocation = (e: React.SyntheticEvent<HTMLInputElement>) => {
    setConfig({
      ...config,
      azureTranslatorLocation: e.currentTarget.value
    })
  }

  const submitLocation = () => {
    window.cheatConfigManager.set('azureTranslatorLocation', config.azureTranslatorLocation)
    update()
  }

  const toggleDevTools = () => {
    const win = window.require('nw.gui').Window.get()
    win.showDevTools()
  }

  return (
    <Fragment>
      <p className="fs-6 m-0 mt-1 w-100 d-block">Configuration</p>
      <hr className="w-100 mt-1 mb-1 d-block" />
      <div className="form-check form-switch mb-1">
        <input
            className="form-check-input"
            type="checkbox"
            role="switch"
            id="config-freezeOnOpen"
            checked={Boolean(config.freezeOnOpen)}
            onChange={freezeOnOpenChange}
        />
        <label className="form-check-label" htmlFor="config-freezeOnOpen">Pause game when opened</label>
      </div>
      <div className="w-100 mb-1">
        <label htmlFor="config-documentZoom" className="form-label fs-6">Zoom (Currently {config.zoom * 100}%)</label>
        <input
          type="range"
          className="form-range"
          id="config-documentZoom"
          min="0"
          max="3"
          step="0.25"
          value={config.zoom ?? 1}
          onChange={updateZoomSize}
        />
      </div>
      <hr className="w-100 mt-1 mb-1 d-block" />
      <p className="d-block mb-1 w-100 fs-6">Azure Translation</p>
      <SmallInputComponent
        name="API Key"
        baseId="config-azure-api-key"
        inputType="text"
        value={config.azureTranslatorApiKey ?? ''}
        handleOnChange={updateApiKey}
        buttonFeatures={['set']}
        setButtonAction={submitApiKey}
      />
      <SmallInputComponent
        name="Endpoint"
        baseId="config-azure-endpoint"
        inputType="text"
        value={config.azureTranslatorEndpoint ?? ''}
        handleOnChange={updateEndpoint}
        buttonFeatures={['set']}
        setButtonAction={submitEndpoint}
      />
      <SmallInputComponent
        name="Location"
        baseId="config-azure-location"
        inputType="text"
        value={config.azureTranslatorLocation ?? ''}
        handleOnChange={updateLocation}
        buttonFeatures={['set']}
        setButtonAction={submitLocation}
      />
      <hr className="w-100 mt-1 mb-1 d-block" />
      <button onClick={toggleDevTools} className="btn btn-success w-100 btn-sm">Toggle Developer Tools</button>
    </Fragment>
  )
}
