import { ICurrentLocationData } from '../../sdk/lib/data/ICurrentLocationData'
import { IMapData } from '../../sdk/lib/data/IMapData'
import { IMapManager } from '../../sdk/lib/IMapManager'

export class MapManager implements IMapManager {
  get current (): ICurrentLocationData {
    if ($gameMap.mapId() === 0) {
      return {
        id: 0,
        name: 'Not yet loaded',
        x: 0,
        y: 0
      }
    }

    return {
      id: $gameMap.mapId(),
      name: $dataMapInfos[$gameMap.mapId()]?.name ?? '',
      x: $gamePlayer._realX,
      y: $gamePlayer._realY
    }
  }

  get list (): IMapData[] {
    return $dataMapInfos
      .filter(x => x)
      .map(x => ({
        id: x.id,
        name: x.name
      }))
  }

  private _validateTeleportArgs (mapId: number, x: number, y: number): boolean {
    return !(!$dataMapInfos[mapId] || x < 0 || y < 0)
  }

  teleport (mapId: number, x: number, y: number): void {
    if (!this._validateTeleportArgs(mapId, x, y)) return

    $gamePlayer.reserveTransfer(mapId, x | 0, y | 0, 0, 0)
    $gamePlayer.setPosition(x | 0, y | 0)
  }
}
