import { inject, injectable } from 'tsyringe'
import { uniqBy } from 'lodash'
import { IMapSwitchesManager } from '../../sdk/lib/IMapSwitchesManager'
import { ISwitchData } from '../../sdk/lib/data/ISwitchData'
import { SwitchManager } from './SwitchManager'

interface IPageCondition {
  valid: boolean;
  targetId: number;
}

@injectable()
export class MapSwitchesManager implements IMapSwitchesManager {
  private readonly _sw: SwitchManager
  private readonly _disabled: boolean = false

  constructor (@inject('SwitchManager') sw: SwitchManager) {
    this._sw = sw
    this._disabled = $dataMap === null || !$dataMap?.events
  }

  /**
   * Fetches available switches on the page events
   * @param {IDataMapEventPage[]} pages Event pages
   * @private
   */
  private _fetchPageData (pages: IDataMapEventPage[]): ISwitchData[] {
    const pageData: ISwitchData[] = []
    for (const page of pages) {
      const conditions: IPageCondition[] = [
        {
          valid: page.conditions.switch1Valid,
          targetId: page.conditions.switch1Id
        },
        {
          valid: page.conditions.switch2Valid,
          targetId: page.conditions.switch2Id
        }
      ]

      for (const { valid, targetId } of conditions) {
        if (!valid) continue

        const switchData = this._sw.getSwitch(targetId) as ISwitchData
        pageData.push({
          ...switchData
        })
      }
    }

    return pageData
  }

  /**
   * Populates all the events in the map.
   * @private
   */
  private _list (): ISwitchData[] {
    const events = $dataMap.events.filter(i => i !== null)

    let data: ISwitchData[] = []
    for (const event of events) {
      data = [...data, ...this._fetchPageData(event.pages)]
    }

    return uniqBy<ISwitchData>(data, 'id')
  }

  get data (): ISwitchData[] {
    if (this._disabled) return []

    return this._list()
  }

  /**
   * Updates all populated switches with a new values
   * @param {boolean} value
   * @private
   */
  private _globalChange (value: boolean): void {
    if (this._disabled) return

    for (const data of this._list()) {
      this._sw.modify(data.id, value)
    }
  }

  disableAllSwitches (): void {
    this._globalChange(false)
  }

  enableAllSwitches (): void {
    this._globalChange(true)
  }

  isDisabled (): boolean {
    return this._disabled
  }

  update (id: number, newValue: boolean): void {
    if (this._disabled) return
    this._sw.modify(id, newValue)
  }
}
