/* global IDataWeapon, IDataArmor, IDataItem */

import { InventoryData } from '../../sdk/lib/data/InventoryData'

export function contractToInventoryData (data: IDataWeapon[] | IDataArmor[] | IDataItem[]) {
  const contract: InventoryData[] = []

  // Well the data is loaded before we do
  if (typeof data === 'undefined' || data === null) return []

  for (let i = 0, l = data.length; i < l; i++) {
    // Ignore if data is null, when name is falsy and name starts with a '-'
    if (!data[i] || !data[i]?.name || data[i]?.name?.startsWith('-')) {
      continue
    }

    contract.push(new InventoryData(data[i]))
  }

  return contract
}
