import React, { Fragment, useEffect, useState } from 'react'
import { IInternalTimerCollection } from '../../../sdk/lib/IInternalTimerCollection'
import { IMapManager } from '../../../sdk/lib/IMapManager'
import { ITranslatorManager, TranslatorTargets } from '../../../sdk/lib/ITranslatorManager'
import { TranslatorProp } from './TranslatorProp'

export function TranslatorInterface (props: TranslatorProp) {
  const lib = window.iLib.require<ITranslatorManager>('TranslatorManager')
  const timer = window.iLib.require<IInternalTimerCollection>('InternalTimers')
  const [active, setActive] = useState('unspecified')
  const [target, setTarget] = useState('no-target')
  const [running, setRunning] = useState(false)
  const [isAutoTranslateActive, setIsAutoTranslateActive] = useState(false)

  useEffect(() => {
    setIsAutoTranslateActive(timer.hasTimer('internal-autoTranslate-task'))
  }, [])

  useEffect(() => {
    if (active === 'unspecified') return

    const provider = props.translatorManager.get(active)
    if (!provider) return

    lib.setTranslator(provider)
  }, [active])

  useEffect(() => {
    if (active === 'no-target') return
    lib.setTranslationTask(target as TranslatorTargets)
  }, [target])

  const handleProviderSelectChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    setActive(event.currentTarget.value)
  }

  const handleTargetSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    setTarget(event.currentTarget.value)
  }

  const runTranslation = () => {
    setRunning(true)
    lib.apply()
      .then(() => {
        setRunning(false)
      })
      .catch(e => {
        console.error(e)
      })
  }

  const toggleAutoTranslation = () => {
    if (isAutoTranslateActive) {
      timer.clearTimer('internal-autoTranslate-task')
      setIsAutoTranslateActive(false)
      return
    }

    if (active === 'unspecified') return
    window.runtimeMemory.store('internal-autoTranslate-providerSelected', active, false)
    timer.registerTimer('internal-autoTranslate-task', setInterval(() => {
      const memory = window.runtimeMemory.get('internal-autoTranslate-lastID')
      const libMap = window.iLib.require<IMapManager>('MapManager')
      const libTranslator = window.iLib.require<ITranslatorManager>('TranslatorManager')

      if (memory === libMap.current.id) return
      const provider = window.runtimeMemory.get('internal-autoTranslate-providerSelected')
      if (!provider) return

      window.runtimeMemory.store('internal-autoTranslate-lastID', libMap.current.id, true)
      const translator = props.translatorManager.get(provider)
      if (!translator) return

      libTranslator.setTranslator(translator)
      libTranslator.setTranslationTask('current_map_interaction')
      libTranslator.apply().catch(console.error)
    }, 150))

    setIsAutoTranslateActive(true)
  }

  return (
    <Fragment>
      <p className="w-100 d-block m-0 mb-1 fs-6">Select Translator</p>
      <select className="form-select form-select-sm w-100 mb-1" value={active} onChange={handleProviderSelectChange}>
        <option value="unspecified">Select translator provider</option>
        {
          props.translatorManager.list().map((x, index) => (
            <option key={index} value={x}>{x}</option>
          ))
        }
      </select>
      {
        active !== 'unspecified' && (
          <Fragment>
            <button className={`btn ${isAutoTranslateActive ? 'btn-danger' : 'btn-success'} w-100 mb-1`} onClick={toggleAutoTranslation}>
              Enable Dialogue Auto Translation
            </button>
            <hr className="w-100 d-block mb-1" />
            <p className="w-100 d-block m-0 mb-1 fs-6">Select Translation Target</p>
            <select className="form-select form-select-sm w-100 mb-1" value={target} onChange={handleTargetSelectionChange}>
              <option value="no-target">Select Translation Target</option>
              {
                ['items', 'weapons', 'armors', 'switches', 'variables', 'map_names']
                  .map((x, index) => (
                    <option key={`targets-${index}`} value={x}>{x}</option>
                  ))
              }
              {
                !isAutoTranslateActive && (
                  <option value="current_map_interaction">current_map_interaction</option>
                )
              }
            </select>
            {
              target !== 'no-target' && (
                <Fragment>
                  <button className="btn btn-success w-100 mb-1" disabled={running} onClick={runTranslation}>Run Translation</button>
                </Fragment>
              )
            }
          </Fragment>
        )
      }
    </Fragment>
  )
}
