import React from 'react'
import { LocationIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { MapSwitchesCheatInterface } from './MapSwitchesCheatInterface'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'

export class MapSwitchesCheatPlugin extends Plugin<{}> {
  constructor () {
    super()
    this._pluginType = PluginTypes.Cheat
    this._pluginId = 'MapSwitchesPlugin'
    this._author = 'Aiko Fujimoto'
    this._name = 'Map Switches Cheat Plugin'
  }

  getIcon (): React.ReactElement {
    return (<LocationIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<MapSwitchesCheatInterface />)
  }
}
