import React from 'react'
import { IInventoryManager } from '../../../../sdk/lib/IInventoryManager'
import { CommonSelectorComponent } from './CommonSelectorComponent'

export function WeaponTabView () {
  const lib = window.iLib.require<IInventoryManager>('WeaponManager')

  return (
    <CommonSelectorComponent baseId="weapons" inventoryName="Weapons" lib={lib} />
  )
}
