import React from 'react'
import { PeopleIcon } from '@primer/octicons-react'
import { PartyCheatInterface } from './PartyCheatInterface'
import { Plugin } from '../../../sdk/Plugin'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'

export class PartyCheatPlugin extends Plugin<{}> {
  constructor () {
    super()
    this._pluginType = PluginTypes.Cheat
    this._pluginId = 'PartyCheatPlugin'
    this._author = 'Aiko Fujimoto'
    this._name = 'Party Cheat Plugin'
  }

  getIcon (): React.ReactElement {
    return (<PeopleIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<PartyCheatInterface />)
  }
}
