import { ITranslator } from '../sdk/lib/ITranslator'

export class ExternalTranslatorManager {
  private readonly _collection: Map<string, ITranslator>

  constructor () {
    this._collection = new Map<string, ITranslator>()
  }

  /**
   * Register the Provider to the client
   * @param {string} providerName
   * @param {ITranslator} providerInstance
   */
  register (providerName: string, providerInstance: ITranslator) {
    if (this._collection.has(providerName)) return
    this._collection.set(providerName, providerInstance)
  }

  /**
   * Checks if certain provider is already exists
   * @param {string} providerName
   */
  has (providerName: string): boolean {
    return this._collection.has(providerName)
  }

  /**
   * Gets the provider instance
   * @param {string} providerName
   */
  get (providerName: string): ITranslator | undefined {
    return this._collection.get(providerName)
  }

  /**
   * Returns list of available translators
   */
  list (): string[] {
    return [...this._collection.keys()]
  }
}
