import React from 'react'
import { LogIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { VariablesCheatInterface } from './VariablesCheatInterface'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'

export class VariablesCheatPlugin extends Plugin<{}> {
  constructor () {
    super()
    this._pluginId = 'VariablesCheatPlugin'
    this._name = 'Variables Cheat Plugin'
    this._pluginType = PluginTypes.Cheat
    this._author = 'Aiko Fujimoto'
  }

  getIcon (): React.ReactElement {
    return (<LogIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<VariablesCheatInterface />)
  }
}
