import React from 'react'
import { CheckIcon, PlusIcon, XIcon } from '@primer/octicons-react'

type AcceptableInputTypes = 'text' | 'number'
export type ButtonFeatures = 'add' | 'set' | 'reset'

interface SmallInputComponentProps {
  name: string;
  baseId: string;
  inputType: AcceptableInputTypes;
  readonly?: boolean;
  value: string | number;
  handleOnChange: (event: React.SyntheticEvent<HTMLInputElement>) => void;
  buttonFeatures: ButtonFeatures[];
  setButtonAction?: () => void;
  addButtonAction?: () => void;
  resetButtonAction?: () => void;
}

export function SmallInputComponent (props: SmallInputComponentProps) {
  const hasFeature = (featureName: ButtonFeatures): boolean => {
    return props.buttonFeatures.filter(x => x === featureName).length > 0
  }

  return (
    <div className="input-group input-group-sm mb-3">
      <span className="input-group-text" id={`${props.baseId}-label`}>{props.name}</span>
      <input
        type={props.inputType}
        className="form-control"
        placeholder={props.name}
        readOnly={props.readonly}
        value={props.value}
        onChange={props.handleOnChange}
        aria-label={props.name}
        aria-describedby={`${props.baseId}-label`} />

      {
        (hasFeature('set') && (typeof props.setButtonAction === 'function')) && (
          <button
            className="btn btn-outline-secondary"
            type="button"
            onClick={props.setButtonAction}>
            <CheckIcon size={12} />
          </button>
        )
      }
      {
        (hasFeature('add') && (typeof props.addButtonAction === 'function')) && (
          <button
            className="btn btn-outline-success"
            type="button"
            onClick={props.addButtonAction}>
            <PlusIcon size={12} />
          </button>
        )
      }
      {
        (hasFeature('reset') && (typeof props.resetButtonAction === 'function')) && (
          <button
            className="btn btn-outline-danger"
            type="button"
            onClick={props.resetButtonAction}>
            <XIcon size={12} />
          </button>
        )
      }
    </div>
  )
}
