import React, { useEffect, useRef } from 'react'
import { Tab } from 'bootstrap'

interface CheatNavigationItem {
  tabIndex: number;
  icon: React.ReactElement;
  id: string;
}

interface CheatNavigationProps {
  navigationItems: CheatNavigationItem[];
}

export function CheatNavigation (props: CheatNavigationProps) {
  const ref = useRef<HTMLButtonElement[]>([])

  useEffect(() => {
    for (const element of ref.current) {
      const tabTrigger = new Tab(element as unknown as Element)

      element.addEventListener('click', e => {
        e.preventDefault()
        tabTrigger.show()
      })
    }
  })

  return (
    <div
      className="nav flex-column nav-pills me-3"
      id="navigation-vertical"
      role="tablist"
      aria-orientation="vertical"
    >
      {props.navigationItems.map((x, index) => (
        <button
          key={index}
          className={`nav-link ${index === 0 && 'active'}`}
          id={`${x.id}-pill`}
          data-bs-toggle="pill"
          data-bs-target={`#${x.id}`}
          type="button"
          role="tab"
          ref={el => el && ref.current.push(el)}
          aria-controls={`#${x.id}`}
          aria-selected={index === 0}
        >
          {x.icon}
        </button>
      ))}
    </div>
  )
}
