import React, { Fragment, useState, useEffect } from 'react'
import { IInternalTimerCollection } from '../../../sdk/lib/IInternalTimerCollection'
import { IPartyManager } from '../../../sdk/lib/IPartyManager'
import { IPlayerManager } from '../../../sdk/lib/IPlayerManager'
import { SmallInputComponent } from '../../components/forms/SmallInputComponent'

interface PartyCheatInterfaceState {
  gold: number;
  activePlayer: number;
  activePlayerToRemove: number;
}

export function PartyCheatInterface () {
  // Dependency
  const lib = window.iLib.require<IPartyManager>('PartyManager')
  const timer = window.iLib.require<IInternalTimerCollection>('InternalTimers')
  const [update, setUpdate] = useState(false)
  const [data, setData] = useState<PartyCheatInterfaceState>({
    gold: 0,
    activePlayer: -1,
    activePlayerToRemove: -1
  })
  const [godModeActive, setGodModeActive] = useState(false)
  const [noClipActive, setNoClipActive] = useState(false)

  useEffect(() => {
    setGodModeActive(timer.hasTimer('internal-party_manager-godMode'))
    setNoClipActive(lib.isNoClipActive())
  }, [])

  useEffect(() => {
    setData({
      ...data,
      gold: lib.partyGold ?? 0
    })
  }, [update])

  const handleGoldChange = (event: React.SyntheticEvent<HTMLInputElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value)) return
    setData({
      ...data,
      gold: value
    })
  }

  const handleCharacterSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value)) return
    setData({
      ...data,
      activePlayer: value
    })
  }

  const handleRemoveCharacterSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value)) return
    setData({
      ...data,
      activePlayerToRemove: value
    })
  }

  const setGoldHandler = () => {
    lib.setGold(data.gold)
    setUpdate(!update)
  }

  const addGoldHandler = () => {
    lib.addGold(data.gold)
    setUpdate(!update)
  }

  const addPlayerToPartyHandler = () => {
    lib.addPlayerToParty(data.activePlayer)
    setUpdate(!update)
  }

  const playerRemovalHandler = () => {
    lib.removePlayerFromParty(data.activePlayerToRemove)
    setData({
      ...data,
      activePlayerToRemove: -1
    })

    setUpdate(!update)
  }

  const toggleNoClip = () => {
    lib.toggleNoClip(!noClipActive)
    setNoClipActive(lib.isNoClipActive())
  }

  const shouldDisableRemovalButton = (): boolean => {
    const currentPartyMemberLength = lib.getPartyMembers().length ?? 0
    if (currentPartyMemberLength <= 1) return true

    // Avoid removing the remaining player
    const partyMember = lib.getPartyMembers().filter(x => x.id === data.activePlayerToRemove)
    if ((partyMember?.length ?? 0) >= 1 && currentPartyMemberLength <= 1) return true

    // Do not remove nonexistent player
    return data.activePlayerToRemove < 1
  }

  const toggleInfiniteLife = () => {
    if (godModeActive) {
      timer.clearTimer('internal-party_manager-godMode')
    }
    if (!godModeActive) {
      timer.registerTimer('internal-party_manager-godMode', setInterval(() => {
        const players = window.iLib.require<IPlayerManager>('PlayerManager')
        for (const player of players.list()) {
          players.setPlayer(player.id)
          players.max()
        }
      }, 150))
    }

    setGodModeActive(!godModeActive)
  }

  return (
    <Fragment>
      <button
        className={`btn ${noClipActive ? 'btn-danger' : 'btn-info'} w-100 btn-sm mb-1`}
        onClick={toggleNoClip}>
          {noClipActive ? 'Disable' : 'Enable'} no-clip
        </button>
      <button
        className={`btn ${godModeActive ? 'btn-danger' : 'btn-info'} w-100 btn-sm`}
        onClick={toggleInfiniteLife}>
        {godModeActive ? 'Disable' : 'Enable'} god mode
      </button>
      <hr className="w-100 d-block mb-1" />
      <SmallInputComponent
        name="Gold"
        baseId="gold"
        inputType="number"
        value={data.gold}
        handleOnChange={handleGoldChange}
        buttonFeatures={['set', 'add']}
        setButtonAction={setGoldHandler}
        addButtonAction={addGoldHandler}
      />
      <div className="w-100 d-block mt-1" />
      <label className="d-block w-100 fs-6">Select player to add</label>
      <select className="form-select form-select-sm w-100" onChange={handleCharacterSelectionChange} value={data.activePlayer}>
        <option value="-1">Select player</option>
        {
          lib.addablePlayers.map((x, index) => (
            <option key={index} value={x.id}>#{x.id} {x.name}</option>
          ))
        }
      </select>
      <button
        className="btn btn-info btn-sm w-100 mt-1"
        disabled={data.activePlayer < 0}
        type="button"
        onClick={addPlayerToPartyHandler}>
        Add Player to Party
      </button>
      <div className="w-100 d-block mt-1" />
      <label className="d-block w-100 fs-6">Select player to remove</label>
      <select className="form-select form-select-sm w-100" onChange={handleRemoveCharacterSelectionChange} value={data.activePlayerToRemove}>
        <option value="-1">Select player</option>
        {
          lib.getPartyMembers().map((x, index) => (
            <option key={index} value={x.id}>#{x.id} {x.name}</option>
          ))
        }
      </select>
      <button
        className="btn btn-danger btn-sm w-100 mt-1"
        disabled={shouldDisableRemovalButton()}
        type="button"
        onClick={playerRemovalHandler}>
        Remove Player to Party
      </button>
    </Fragment>
  )
}
