import React from 'react'
import { CheckCircleIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'
import { SwitchCheatInterface } from './SwitchCheatInterface'

export class SwitchCheatPlugin extends Plugin<{}> {
  constructor () {
    super()
    this._pluginId = 'SwitchCheatPlugin'
    this._name = 'Switch Cheat Plugin'
    this._pluginType = PluginTypes.Cheat
    this._author = 'Aiko Fujimoto'
  }

  getIcon (): React.ReactElement {
    return (<CheckCircleIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<SwitchCheatInterface />)
  }
}
