/**
 * Creates or Gets the root if available.
 */
export function getOrCreateRoot (): Element {
  if (!document.getElementById('root')) {
    const el = document.createElement('div')
    el.id = 'root'

    const parent = document.getElementsByTagName('body')[0] as HTMLBodyElement
    parent.prepend(el)
  }

  return document.getElementById('root') as unknown as Element
}
