import React from 'react'
import { BugIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'
import { EnemyCheatInterface } from './EnemyCheatInterface'

export class EnemyCheatPlugin extends Plugin<{}> {
  constructor () {
    super()
    this._pluginType = PluginTypes.Cheat
    this._pluginId = 'EnemyCheatPlugin'
    this._author = 'Aiko Fujimoto'
    this._name = 'Enemy Cheat Plugin'
  }

  getIcon (): React.ReactElement {
    return (<BugIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<EnemyCheatInterface />)
  }
}
