import React from 'react'
import { IInventoryManager } from '../../../../sdk/lib/IInventoryManager'
import { CommonSelectorComponent } from './CommonSelectorComponent'

export function ArmorTabView () {
  const lib = window.iLib.require<IInventoryManager>('ArmorManager')

  return (
    <CommonSelectorComponent baseId="armors" inventoryName="Armors" lib={lib} />
  )
}
