import React from 'react'
import { TelescopeIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'
import { MapCheatInterface } from './MapCheatInterface'

export class MapCheatPlugin extends Plugin<{}> {
  constructor () {
    super()
    this._pluginType = PluginTypes.Cheat
    this._pluginId = 'MapCheatPlugin'
    this._author = 'Aiko Fujimoto'
    this._name = 'Map Cheat Plugin'
  }

  getIcon (): React.ReactElement {
    return (<TelescopeIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<MapCheatInterface />)
  }
}
