import { singleton } from 'tsyringe'
import { IVariableData } from '../../sdk/lib/data/IVariableData'
import { VariableTypes, IVariablesManager } from '../../sdk/lib/IVariablesManager'

@singleton()
export class VariablesManager implements IVariablesManager {
  private _validateValues (type: VariableTypes, value: string | number | null): boolean {
    if (type === 'null' && value === null) return true
    if (type === 'string' && (typeof value === 'string')) {
      return value.replace(/\s/g, '').length > 0
    }
    if (type === 'number' && (typeof value === 'number')) {
      return Number.isInteger(value)
    }

    return false
  }

  private _fetchVariableValue (value: string | number | null): VariableTypes {
    if (typeof value === 'string') return 'string'
    if (typeof value === 'number') return 'number'
    return 'number'
  }

  private _list (): IVariableData[] {
    return $dataSystem.variables
      .map((x, index) => ({
        id: index,
        key: x,
        type: this._fetchVariableValue($gameVariables.value(index)),
        value: $gameVariables.value(index)
      }))
      .filter(x => x.key && x.key?.replace(/\s/g, '').length > 0)
  }

  fill (type: VariableTypes, value: string | number | null): void {
    if (!this._validateValues(type, value)) return

    for (const variable of this._list()) {
      $gameVariables.setValue(variable.id, value)
    }
  }

  get list (): IVariableData[] {
    return this._list()
  }

  modify (id: number, type: VariableTypes, value: string | number | null): void {
    // Ignore update when validate fails
    if (!this._validateValues(type, value)) return

    if (type === 'null') {
      $gameVariables.setValue(id, null)
      return
    }

    $gameVariables.setValue(id, value)
  }
}
