/* global $dataMapInfos ,$dataItems, $dataWeapons, $dataArmors, $dataSystem, IDataMap, $dataMap */

import { contractToTranslationItem } from './contractToTranslationItem'
import { TranslatorTargets } from '../../sdk/lib/ITranslatorManager'
import { ITranslationItem } from '../../sdk/lib/data/ITranslationBucket'

function flattenInteractions (data: IDataMap): string[] {
  // Map is not loaded. Do nothing.
  if (data.autoplayBgm === undefined) return []

  const flatten: string[] = []
  for (const event of $dataMap.events) {
    if (!event) continue
    for (const page of event.pages) {
      for (const item of page.list) {
        if (item.code === 401) {
          for (let i = 0, l = item.parameters.length; i < l; i++) {
            flatten.push(`${item.parameters[i]}`)
          }
        }
      }
    }
  }

  return flatten
}

export function targetsToTranslationItem (target: TranslatorTargets | string): ITranslationItem[] {
  switch (target) {
    case 'items':
      return contractToTranslationItem($dataItems)
    case 'weapons':
      return contractToTranslationItem($dataWeapons)
    case 'armors':
      return contractToTranslationItem($dataArmors)
    case 'switches':
      return $dataSystem.switches
        .filter(x => x)
        .map((x) => ({
          value: x,
          translated: ''
        }))
    case 'variables':
      return $dataSystem.variables
        .filter(x => x)
        .map((x) => ({
          value: x,
          translated: ''
        }))
    case 'map_names':
      return $dataMapInfos
        .filter(x => x)
        .map((x, index) => ({
          value: x.name,
          translated: ''
        }))
    case 'current_map_interaction':
      if ($dataMap.autoplayBgm === undefined) return []
      return flattenInteractions($dataMap)
        .map(x => ({
          value: x,
          translated: ''
        }))
  }

  return []
}
