import { singleton } from 'tsyringe'
import { IBasicPlayerData } from '../../sdk/lib/data/IBasicPlayerData'
import { IPlayerManager } from '../../sdk/lib/IPlayerManager'

@singleton()
export class PlayerManager implements IPlayerManager {
  private _currentPlayerData: Game_Actor | null = null

  /**
   * Selects the player
   * @param {number} id ID of the player
   */
  setPlayer (id: number) {
    this._currentPlayerData = $gameActors.actor(id)
  }

  /**
   * Unsets the player
   */
  unsetPlayer () {
    this._currentPlayerData = null
  }

  /**
   * Sets the name of the player.
   * @param {string} name
   */
  setName (name: string) {
    this._currentPlayerData?.setName(name)
  }

  /**
   * Checks if the player exists
   * @param {number} id ID of the player
   * @return {boolean}
   */
  isPlayerExist (id: number): boolean {
    return $gameActors.actor(id) !== null
  }

  /**
   * Adds HP to the current selected character
   * @param {number} value Value to increment the hp of the current selected player
   */
  addHp (value: number) {
    this._currentPlayerData?.setHp(this._currentPlayerData?._hp + value)
  }

  /**
   * Sets HP to the current selected character
   * @param {number} value New value
   */
  setHp (value: number) {
    this._currentPlayerData?.setHp(value)
  }

  /**
   * Adds MP to the current selected character
   * @param {number} value Value to increment the mp of the current selected player
   */
  addMp (value: number) {
    this._currentPlayerData?.setMp(this._currentPlayerData?._mp + value)
  }

  /**
   * Sets MP to the current selected character
   * @param {number} value New value
   */
  setMp (value: number) {
    this._currentPlayerData?.setMp(value)
  }

  /**
   * Adds TP to the current selected character
   * @param {number} value Value to increment the tp of the current selected player
   */
  addTp (value: number) {
    this._currentPlayerData?.setTp(this._currentPlayerData?._tp + value)
  }

  /**
   * Sets TP to the current selected character
   * @param {number} value New value
   */
  setTp (value: number) {
    this._currentPlayerData?.setTp(value)
  }

  /**
   * Adds XP to the current selected character
   * @param {number} value Value to increment the xp of the current selected player
   */
  addXp (value: number) {
    this._currentPlayerData?.gainExp(value)
  }

  /**
   * Max all stats (except XP)
   */
  max () {
    this.setHp(999999)
    this.setTp(999999)
    this.setMp(999999)
  }

  /**
   * Gets the current active actor
   */
  get character (): Game_Actor | null {
    return this._currentPlayerData
  }

  /**
   * Lists all available players
   */
  list (): IBasicPlayerData[] {
    return $gameActors._data
      .filter(x => x)
      .map(x => ({
        id: x._actorId,
        name: x._name
      }))
  }
}
