import { contractToInventoryData } from '../contracts/contractToInventoryData'
import { InventoryManager } from './InventoryManager'

export class InventoryArmorManager extends InventoryManager {
  constructor () {
    super()

    this._inventoryData = contractToInventoryData($dataArmors)
  }

  protected queryCount (itemId: number): number {
    return $gameParty._armors[itemId]
  }

  set (itemId: number, count: number): void {
    $gameParty._armors[itemId] = count
  }
}
