import { contractToInventoryData } from '../contracts/contractToInventoryData'
import { InventoryManager } from './InventoryManager'

export class InventoryWeaponManager extends InventoryManager {
  constructor () {
    super()

    this._inventoryData = contractToInventoryData($dataWeapons)
  }

  protected queryCount (itemId: number): number {
    return $gameParty._weapons[itemId]
  }

  set (itemId: number, count: number): void {
    $gameParty._weapons[itemId] = count
  }
}
