import fs from 'fs'
import path from 'path'
import { ITranslationProgress } from '../../sdk/lib/data/ITranslationProgress'
import { ITranslator } from '../../sdk/lib/ITranslator'
import { ITranslatorManager, TranslatorTargets } from '../../sdk/lib/ITranslatorManager'
import { ITranslationBucketStatus, ITranslationItem } from '../../sdk/lib/data/ITranslationBucket'
import { targetsToTranslationItem } from '../contracts/targetsToTranslationItem'
import { FileSystemPromises as fsp } from '../utils/fs-promises'

function flattenTranslationBucket (translationBucket: ITranslationBucketStatus[], revert?: boolean): string[] {
  const data: string[] = []

  for (const bucket of translationBucket) {
    for (const item of bucket.words) {
      data.push(revert ? item.value : item.translated)
    }
  }

  return data
}

function assignToInventoryData (data: IDataWeapon[] | IDataArmor[] | IDataItem[], translated: string[]) {
  let translatedTracker = 0
  for (let i = 0, l = data.length; i < l; i++) {
    if (!data[i] || !data[i]?.name || data[i]?.name?.startsWith('-')) continue
    data[i].name = translated[translatedTracker]
    translatedTracker++
  }
}

function assignToSystemData (data: IDataSystem['switches'] | IDataSystem['variables'], translated: string[]) {
  let translatedTracker = 0
  for (let i = 0, l = data.length; i < l; i++) {
    if (!data[i]) continue
    data[i] = translated[translatedTracker]
    translatedTracker++
  }
}

function assignToDataMap (data: IDataMapInfo[], translated: string[]) {
  let translatedTracker = 0
  for (let i = 0, l = data.length; i < l; i++) {
    if (!data[i]) continue
    data[i].name = translated[translatedTracker]
    translatedTracker++
  }
}

function assignMapInformation (data: IDataMap, translated: string[]) {
  // Map is not loaded. Do nothing.
  if (data.autoplayBgm === undefined) return

  let translatedPointer = 0
  for (const event of $dataMap.events) {
    if (!event) continue
    for (const page of event.pages) {
      for (const item of page.list) {
        if (item.code === 401) {
          for (let i = 0, l = item.parameters.length; i < l; i++) {
            item.parameters[i] = translated[translatedPointer]
            translatedPointer++
          }
        }
      }
    }
  }
}

export class TranslatorManager implements ITranslatorManager {
  private _translator?: ITranslator
  private _target?: TranslatorTargets
  private _dataTargets: ITranslationItem[] = []
  private readonly _translationCacheLocation = path.join(process?.mainModule?.filename ? path.dirname(process?.mainModule?.filename) : process.cwd(), './translations')

  private _getFileName (): string {
    return this._target === 'current_map_interaction'
      ? `./${this._target}-${$gameMap.mapId()}.json`
      : `./${this._target}.json`
  }

  private _hasTranslationCache (): boolean {
    if (!this._target) return false
    return fs.existsSync(path.join(this._translationCacheLocation, this._getFileName()))
  }

  private async _storeTranslationCache (data: ITranslationBucketStatus[]): Promise<void> {
    // Re-create directories if they do not exist.
    if (!fs.existsSync(this._translationCacheLocation)) {
      await fsp.mkdir(this._translationCacheLocation, { recursive: true })
    }

    const writePath = path.join(this._translationCacheLocation, this._getFileName())
    await fsp.writeFile(writePath, JSON.stringify(data, undefined, 2), 'utf-8')
  }

  private async _fetchTranslationCache (): Promise<ITranslationBucketStatus[]> {
    const cachePath = path.join(this._translationCacheLocation, this._getFileName())
    if (!fs.existsSync(cachePath)) return []

    const data = await fsp.readFile(cachePath, 'utf-8')
    return JSON.parse(data) as ITranslationBucketStatus[]
  }

  private _setTarget () {
    this._dataTargets = targetsToTranslationItem(this._target ?? '')
  }

  private _assignTargets (data: ITranslationBucketStatus[], revert?: boolean) {
    const flatten = flattenTranslationBucket(data, revert)
    switch (this._target) {
      case 'items':
        assignToInventoryData($dataItems, flatten)
        break
      case 'weapons':
        assignToInventoryData($dataWeapons, flatten)
        break
      case 'armors':
        assignToInventoryData($dataArmors, flatten)
        break
      case 'switches':
        assignToSystemData($dataSystem.switches, flatten)
        break
      case 'variables':
        assignToSystemData($dataSystem.variables, flatten)
        break
      case 'map_names':
        assignToDataMap($dataMapInfos, flatten)
        break
      case 'current_map_interaction':
        assignMapInformation($dataMap, flatten)
        break
    }
  }

  private async _revertTargets (): Promise<void> {
    if (this._hasTranslationCache()) {
      const cache = await this._fetchTranslationCache()
      this._assignTargets(cache, true)
    }
  }

  async apply (): Promise<void> {
    if (!this._translator) return
    if (this._dataTargets.length <= 0) return

    if (this._hasTranslationCache()) {
      const cache = await this._fetchTranslationCache()
      this._assignTargets(cache)

      return
    }

    this._translator.queueJob(this._dataTargets)
    await this._translator.runTranslationTask()

    if (this._translator.progress().failedCount > 0) {
      await this.apply()
    }

    // Apply the changes
    await this._storeTranslationCache(this._translator.getCompleted())
    this._assignTargets(this._translator.getCompleted())
  }

  progress (): ITranslationProgress {
    if (!this._translator) return { completedCount: 0, queuedCount: 0, failedCount: 0, progress: 0 }
    return this._translator.progress()
  }

  async revert (): Promise<void> {
    if (!this._translator) return
    await this._revertTargets()
  }

  setTranslationTask (target: TranslatorTargets): void {
    if (!this._translator) return

    this._target = target
    this._setTarget()
    this._translator.clearJob()
  }

  setTranslator (translator: ITranslator): void {
    this._translator = translator
  }
}
