import { IInGameEnemy } from '../../sdk/lib/data/IInGameEnemy'

/* global Game_Enemy */

export class InGameEnemy implements IInGameEnemy {
  private readonly _enemy: Game_Enemy
  private readonly _initialHp: number
  private _dead: boolean = false

  constructor (enemy: Game_Enemy) {
    this._enemy = enemy
    this._initialHp = Number(enemy.hp)
    this._dead = enemy.isDead()
  }

  get hp (): number {
    return this._enemy.hp
  }

  get id (): number {
    return this._enemy._enemyId
  }

  get name (): string {
    return this._enemy.battlerName()
  }

  kill () {
    if (this._dead) return
    this._enemy.die()
    this._dead = true
  }

  singleHitHp () {
    if (this._dead) return
    this._enemy.setHp(1)
  }

  setHp (hp: number) {
    if (this._dead) return
    this._enemy.setHp(hp)
  }

  restore () {
    if (this._dead) return
    this._enemy.setHp(this._initialHp)
  }
}
