import React, { Fragment, useRef, useEffect } from 'react'
import { Tab } from 'bootstrap'

interface TabElement {
  title: string;
  baseId: string;
}

interface HorizontalTabComponentProps {
  tabs: TabElement[],
  children: React.ReactElement[]
}

export function HorizontalTabComponent (props: HorizontalTabComponentProps) {
  const ref = useRef<HTMLButtonElement[]>([])

  useEffect(() => {
    if (!ref?.current) return
    const elementTree = ref.current

    for (const element of elementTree) {
      if (element.getAttribute('role') !== 'tab') continue
      const presentation = new Tab(element as unknown as Element)

      element.addEventListener('click', e => {
        e.preventDefault()
        presentation.show()
      })
    }
  })

  return (
    <Fragment>
      <nav>
        <div className="nav nav-tabs" role="tablist">
          {
            props.tabs.map((x, index) => (
              <button
                key={index}
                className={`nav-link ${index === 0 && 'active'}`}
                id={`${x.baseId}-tab`}
                data-bs-toggle="tab"
                data-bs-target={`#${x.baseId}-content`}
                type="button"
                role="tab"
                ref={el => el && ref.current.push(el)}
                aria-controls={`${x.baseId}-content`}
                aria-selected={index === 0}>
                {x.title}
              </button>
            ))
          }
        </div>
      </nav>
      <div className="tab-content d-block w-100">
        {
          props.children.map((Element, index) => (
            <div
              key={index}
              id={`${props.tabs[index].baseId}-content`}
              className={`tab-pane fade ${index === 0 && 'show active'}`}
              role="tabpanel"
              aria-labelledby={`${props.tabs[index].baseId}-tab`}>
              {Element}
            </div>
          ))
        }
      </div>
    </Fragment>
  )
}
