import React from 'react'
import { IInventoryManager } from '../../../../sdk/lib/IInventoryManager'
import { CommonSelectorComponent } from './CommonSelectorComponent'

export function ItemTabView () {
  const lib = window.iLib.require<IInventoryManager>('ItemManager')

  return (
    <CommonSelectorComponent baseId="items" inventoryName="Items" lib={lib} />
  )
}
