import React from 'react'
import { InboxIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'
import { InventoryCheatInterface } from './InventoryCheatInterface'

export class InventoryCheatPlugin extends Plugin<{}> {
  constructor () {
    super()
    this._pluginType = PluginTypes.Cheat
    this._pluginId = 'InventoryCheatPlugin'
    this._author = 'Aiko Fujimoto'
    this._name = 'Inventory Cheat Plugin'
  }

  getIcon (): React.ReactElement {
    return (<InboxIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<InventoryCheatInterface />)
  }
}
