import { container, DependencyContainer, Lifecycle } from 'tsyringe'

import { IInventoryManager } from '../sdk/lib/IInventoryManager'
import { IPartyManager } from '../sdk/lib/IPartyManager'
import { IPlayerManager } from '../sdk/lib/IPlayerManager'
import { PlayerManager } from './services/PlayerManager'
import { PartyManager } from './services/PartyManager'
import { InventoryArmorManager } from './services/InventoryArmorManager'
import { InventoryItemManager } from './services/InventoryItemManager'
import { InventoryWeaponManager } from './services/InventoryWeaponManager'
import { ISwitchManager } from '../sdk/lib/ISwitchManager'
import { SwitchManager } from './services/SwitchManager'
import { IVariablesManager } from '../sdk/lib/IVariablesManager'
import { VariablesManager } from './services/VariablesManager'
import { IMapSwitchesManager } from '../sdk/lib/IMapSwitchesManager'
import { MapSwitchesManager } from './services/MapSwitchesManager'
import { IMapManager } from '../sdk/lib/IMapManager'
import { MapManager } from './services/MapManager'
import { IInternalTimerCollection } from '../sdk/lib/IInternalTimerCollection'
import { InternalTimerCollection } from './services/InternalTimerCollection'
import { IEnemyManager } from '../sdk/lib/IEnemyManager'
import { EnemyManager } from './services/EnemyManager'
import { ITranslatorManager } from '../sdk/lib/ITranslatorManager'
import { TranslatorManager } from './services/TranslatorManager'

export class LibraryBootstrapper {
  private readonly _container: DependencyContainer

  constructor () {
    this._container = container.createChildContainer()
  }

  /**
   * Registers the library
   */
  registerLibraries (): void {
    this._container.register<IPlayerManager>('PlayerManager', PlayerManager, { lifecycle: Lifecycle.Singleton })
    this._container.register<IPartyManager>('PartyManager', PartyManager, { lifecycle: Lifecycle.Singleton })
    this._container.register<IInventoryManager>('ArmorManager', InventoryArmorManager, { lifecycle: Lifecycle.Transient })
    this._container.register<IInventoryManager>('ItemManager', InventoryItemManager, { lifecycle: Lifecycle.Transient })
    this._container.register<IInventoryManager>('WeaponManager', InventoryWeaponManager, { lifecycle: Lifecycle.Transient })
    this._container.register<ISwitchManager>('SwitchManager', SwitchManager, { lifecycle: Lifecycle.Singleton })
    this._container.register<IVariablesManager>('VariablesManager', VariablesManager, { lifecycle: Lifecycle.Singleton })
    this._container.register<IMapSwitchesManager>('MapSwitchesManager', MapSwitchesManager, { lifecycle: Lifecycle.Transient })
    this._container.register<IMapManager>('MapManager', MapManager, { lifecycle: Lifecycle.Transient })
    this._container.register<IInternalTimerCollection>('InternalTimers', InternalTimerCollection, { lifecycle: Lifecycle.Singleton })
    this._container.register<IEnemyManager>('EnemyManager', EnemyManager, { lifecycle: Lifecycle.Transient })
    this._container.register<ITranslatorManager>('TranslatorManager', TranslatorManager, { lifecycle: Lifecycle.Singleton })
  }

  /**
   * Gets the required library registered.
   */
  getRequiredLibrary <T> (className: string): T {
    return this._container.resolve<T>(className)
  }
}
