import axios, { AxiosInstance } from 'axios'
import { chunk } from 'lodash'
import { ITranslator } from '../../sdk/lib/ITranslator'
import { ITranslationBucketStatus, ITranslationItem } from '../../sdk/lib/data/ITranslationBucket'
import { ITranslationProgress } from '../../sdk/lib/data/ITranslationProgress'
import { TranslationStatus } from '../../sdk/lib/enums/TranslationStatus'

/**
 * Azure Translator Credentials
 */
interface Authentication {
  /**
   * User's Subscription key. Controllable using the environment variable
   * AZURE_TRANSLATOR_API_KEY or can be configured inside the tool.
   */
  key?: string;
  /**
   * User's endpoint provided by the Azure service. Controllable using
   * the environment variable AZURE_TRANSLATOR_ENDPOINT or can be
   * configured inside the tool.
   */
  endpoint?: string;
  /**
   * Location supplied by the Azure service. Controllable using the
   * environment variable AZURE_TRANSLATOR_LOCATION.
   */
  location?: string;
}

interface TranslationResponse {
  translations: { text: string }[]
}

export class AzureTranslator implements ITranslator {
  private readonly _authentication: Authentication = {
    key: window.cheatConfigManager.get('azureTranslatorApiKey'),
    endpoint: window.cheatConfigManager.get('azureTranslatorEndpoint'),
    location: window.cheatConfigManager.get('azureTranslatorLocation')
  }

  private readonly _disabled: boolean = true

  private readonly _bucket: ITranslationBucketStatus[] = []

  private readonly _request?: AxiosInstance

  constructor () {
    const { key, endpoint, location } = this._authentication
    if (key && endpoint && location) {
      this._disabled = false

      this._request = axios.create({
        baseURL: endpoint,
        headers: {
          'Ocp-Apim-Subscription-Key': key,
          'Ocp-Apim-Subscription-Region': location,
          'Content-Type': 'application/json'
        },
        params: {
          'api-version': '3.0',
          to: ['en']
        },
        responseType: 'json'
      })
    }
  }

  private _makeBuckets (words: ITranslationItem[]) {
    const buckets = chunk<ITranslationItem>(words, 100)
    for (const bucket of buckets) {
      this._bucket.push({
        words: bucket,
        status: TranslationStatus.Queued
      })
    }
  }

  queueJob (words: ITranslationItem[]) {
    if (this._disabled) return

    // Check if the chunks in the bucket are not yet filled.
    if (this._bucket[this._bucket.length - 1]) {
      const lastBucketLength = this._bucket[this._bucket.length - 1].words.length
      if (lastBucketLength < 100) {
        this._bucket[this._bucket.length - 1].words.push(...words.slice(0, 100 - lastBucketLength))

        // Add the remaining as new buckets
        this._makeBuckets(words.slice(100 - lastBucketLength))
        return
      }
    }

    this._makeBuckets(words)
  }

  clearJob () {
    this._bucket.length = 0
  }

  progress (): ITranslationProgress {
    const total = this._bucket.length
    const completed = this._bucket.filter(x => x.status === TranslationStatus.Finished).length
    const failed = this._bucket.filter(x => x.status === TranslationStatus.Failed).length
    const queued = this._bucket.filter(x => x.status === TranslationStatus.Queued).length

    return {
      progress: ((total + completed) / total) * 100,
      completedCount: completed,
      failedCount: failed,
      queuedCount: queued
    }
  }

  getCompleted (): ITranslationBucketStatus[] {
    return this._bucket.filter(x => x.status === TranslationStatus.Finished)
  }

  async runTranslationTask (): Promise<void> {
    if (this._disabled || !this._request) return

    for (const bucket of this._bucket.filter(x => x.status !== TranslationStatus.Finished)) {
      try {
        const data = bucket.words.map(x => ({ text: x.value }))
        const result = await this._request.post<TranslationResponse[]>('/translate', data)

        result.data.forEach((x, index) => {
          bucket.words[index].translated = x.translations[0].text
        })

        bucket.status = TranslationStatus.Finished
      } catch (e: any) {
        bucket.status = TranslationStatus.Failed
      }
    }
  }
}
