import React, { Fragment } from 'react'

import '../scss/cheat-container.scss'

interface CheatContainerProps {
    toggled: boolean;
    children: React.ReactElement;
}

export function CheatContainer (props: CheatContainerProps) {
  return (
    <Fragment>
      {
        props.toggled
          ? (
              <div className="cheat-container">
                {props.children}
              </div>
            )
          : ''
      }
    </Fragment>
  )
}
