import fs from 'fs'
import path from 'path'
import os from 'os'
import { defaultConfiguration } from './data/defaultConfiguration'
import { FileSystemPromises as fsp } from './utils/fs-promises'

export interface ConfigurationBase {
  [key: string]: any
}

export interface Configuration {
  data: ConfigurationBase
}

export class ConfigurationManager {
  private readonly _config: Map<string, any>
  private readonly _configHome = path.join(os.homedir(), './.rmmv-util')

  constructor () {
    this._config = new Map<string, any>()
  }

  private _useDefaults () {
    for (const item of Object.keys(defaultConfiguration.data)) {
      this._config.set(item, defaultConfiguration.data[item])
    }
  }

  async load (): Promise<void> {
    if (!fs.existsSync(path.join(this._configHome, './config.json'))) {
      this._useDefaults()
      return
    }

    try {
      const file = await fsp.readFile(path.join(this._configHome, './config.json'), 'utf-8')
      const json = JSON.parse(file) as Configuration

      if (json.data) {
        for (const item of Object.keys(json.data)) {
          this._config.set(item, json.data[item])
        }
      }

      return
    } catch (e) {
      this._useDefaults()
    }

    this._useDefaults()
  }

  async save (): Promise<void> {
    if (!fs.existsSync(this._configHome)) {
      await fsp.mkdir(this._configHome, { recursive: true })
    }

    const config: Configuration = { data: {} }
    for (const item of this._config.keys()) {
      config.data[item] = this._config.get(item)
    }

    await fsp.writeFile(path.join(this._configHome, './config.json'), JSON.stringify(config, undefined, 2), 'utf-8')
  }

  /**
   * Get the value of configuration item
   * @param {string} item
   */
  read (item: string): any | undefined {
    return this._config.get(item)
  }

  /**
   * Reads all configuration items
   */
  readAll (): ConfigurationBase {
    const data: ConfigurationBase = {}

    for (const key of this._config.keys()) {
      data[key] = this._config.get(key)
    }

    return data
  }

  /**
   * Set/Update a value from configuration
   * @param {string} item target configuration item
   * @param {any} value
   */
  set (item: string, value: any): void {
    this._config.set(item, value)
  }
}
