/* global SceneManager */

import React, { useState, useEffect } from 'react'
import { CheatContainer } from './components/CheatContainer'
import { CheatNavigation } from './components/CheatNavigation'

import './scss/cheat-wrapper.scss'
import { PluginManager } from './PluginManager'
import { PluginTypes } from '../sdk/enums/PluginTypes'

interface ClientMainProps {
  pluginManager: PluginManager
}

export function ClientMain (props: ClientMainProps) {
  const [active, setActive] = useState(false)

  useEffect(() => {
    if (!window.cheatConfigManager.get('freezeOnOpen')) {
      if (!SceneManager._scene._active) SceneManager._scene._active = true
      return
    }
    // Pause the game when the cheat window is opened.
    if (typeof SceneManager === 'undefined' || SceneManager === null) return
    SceneManager._scene._active = !active
  }, [active])

  function handleToggle () {
    setActive(!active)
  }

  const cheatInterfaces = props.pluginManager.getPlugins(PluginTypes.Cheat)
  return (
    <div className="cheat-wrapper">
      <button className="btn btn-primary btn-sm" onClick={handleToggle}>
        {active ? 'Close' : 'Open'} Cheat Tools
      </button>
      <CheatContainer toggled={active}>
        <div className="d-flex align-items-start">
          <CheatNavigation
            navigationItems={cheatInterfaces.map((x, index) => ({
              tabIndex: index,
              icon: x.getIcon(),
              id: x.getId()
            }))}
          />
          <div className="tab-content" id="content-view">
            {cheatInterfaces.map((x, index) => (
              <div
                key={index}
                className={`tab-pane fade ${index === 0 && 'show active'}`}
                id={x.getId()}
                role="tabpanel"
                aria-labelledby={`#${x.getId()}-pill`}
                tabIndex={index}
              >
                {x.getInterface()}
              </div>
            ))}
          </div>
        </div>
      </CheatContainer>
    </div>
  )
}
