import React, { Fragment, useState } from 'react'
import { IInventoryManager } from '../../../../lib/IInventoryManager'
import { IInventoryInformation } from '../../../../lib/data/IInventoryInformation'
import { SmallInputComponent } from '../../../components/forms/SmallInputComponent'

interface CommonSelectorComponentState {
  selectedItem: number;
  fieldValues: IInventoryInformation;
  getAllValue: number;
}

interface CommonSelectorComponentProps {
  baseId: string;
  inventoryName: string;
  lib: IInventoryManager;
}

export function CommonSelectorComponent (props: CommonSelectorComponentProps) {
  const { lib, inventoryName, baseId } = props
  const [data, setData] = useState<CommonSelectorComponentState>({
    selectedItem: -1,
    fieldValues: {
      id: -1,
      name: '',
      count: 0
    },
    getAllValue: 1
  })

  const handleUpdateValues = (value: number) => {
    const inventoryData = lib.getItemInformation(value)
    if (!inventoryData) return

    setData({
      ...data,
      selectedItem: value,
      fieldValues: {
        id: inventoryData.id,
        name: inventoryData.name,
        count: inventoryData.count ?? 0
      }
    })
  }

  const handleSelectionChange = (event: React.SyntheticEvent<HTMLSelectElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value)) return
    handleUpdateValues(value)
  }

  const handleValueChange = (event: React.SyntheticEvent<HTMLInputElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value) || value < 0) return

    setData(lastState => ({
      ...lastState,
      fieldValues: {
        ...lastState.fieldValues,
        count: value
      }
    }))
  }

  const handleSubmit = () => {
    lib.set(data.fieldValues.id, data.fieldValues.count)
    handleUpdateValues(data.fieldValues.id)
  }

  const handleGetAllInputValueChange = (event: React.SyntheticEvent<HTMLInputElement>) => {
    const value = Number(event.currentTarget.value)

    if (!Number.isInteger(value) || value < 0) return

    setData(lastState => ({
      ...lastState,
      getAllValue: value
    }))
  }

  const handleGetAllSubmit = () => {
    lib.getAll(data.getAllValue)
  }

  const handleRemoveAllSubmit = () => {
    lib.removeAll()
  }

  return (
    <Fragment>
      <label className="d-block w-100 fs-6">Select {inventoryName}</label>
      <select className="form-select form-select-sm w-100" value={data.selectedItem} onChange={handleSelectionChange}>
        <option value="-1">Select {inventoryName}</option>
        {
          lib.items.map((x, index) => (
            <option key={index} value={x.id}>#{x.id} {x.name}</option>
          ))
        }
      </select>
      <div className="d-block w-100 mt-1" />
      {
        data.selectedItem > 0 && (
          <SmallInputComponent
            name={data.fieldValues.name}
            baseId={`${baseId}-field-id${data.fieldValues.id}`}
            inputType={'number'}
            value={data.fieldValues.count ?? 0}
            handleOnChange={handleValueChange}
            buttonFeatures={['set']}
            setButtonAction={handleSubmit}
          />
        )
      }
      <hr className="w-100 mt-1 mb-1" />
      <p className="w-100 fs-6 mb-1 p-0">All inventory actions</p>
      <SmallInputComponent
        name="Get all"
        baseId={`${baseId}-getAll`}
        inputType={'number'}
        value={data.getAllValue}
        handleOnChange={handleGetAllInputValueChange}
        buttonFeatures={['set']}
        setButtonAction={handleGetAllSubmit}
      />
      <button
        className="btn btn-small btn-danger w-100"
        type="button"
        onClick={handleRemoveAllSubmit}>
        Remove ALL {inventoryName}
      </button>
    </Fragment>
  )
}
