export class RuntimeStorage {
  private readonly _storage: Map<string, any>

  constructor () {
    this._storage = new Map<string, any>()
  }

  /**
   * Store variables
   * @param {string} name
   * @param {any} value
   * @param {boolean} override Override stored variable
   */
  store (name: string, value: any, override: boolean = false) {
    if (this._storage.has(name) && !override) return
    this._storage.set(name, value)
  }

  /**
   * Get stored variables
   * @param name
   */
  get (name: string): any | undefined {
    return this._storage.get(name)
  }
}
