import { IPlugin } from '../sdk/IPlugin'
import { PluginTypes } from '../sdk/enums/PluginTypes'

import fs from 'fs'
import path from 'path'

export class PluginManager {
  private readonly _plugins: Map<string, IPlugin> = new Map<string, IPlugin>()

  /**
   * Gets all loaded plugins
   * @param {PluginTypes | null} ofType plugin to fetch. Do not specify anything to fetch all
   * @return {Plugin[]}
   */
  getPlugins (ofType?: PluginTypes): IPlugin[] {
    return [...this._plugins.values()].filter(x => {
      if (ofType != null) return x.getPluginType() === ofType
      return x
    })
  }

  /**
   * Unloads the plugin
   * @param {string} pluginId ID of the plugin to unload
   */
  unloadPlugin (pluginId: string): void {
    // Do nothing when no plugin was found.
    if (!this._plugins.has(pluginId)) return
    this._plugins.delete(pluginId)
  }

  /**
   * Registers plugins to plugin manager
   * @param {IPlugin} plugin Plugin to load
   */
  registerPlugin (plugin: IPlugin): void {
    // Don't register existing plugins.
    if (this._plugins.has(plugin.getId())) return
    this._plugins.set(plugin.getId(), plugin)
  }

  /**
   * Loads plugins in a specific folder.
   */
  loadPluginsFromFolder (): void {
    // Check if the plugins folder is present.
    const pluginFolder = path.join(process.cwd(), './www/hooks/plugins')
    if (!fs.existsSync(pluginFolder)) {
      console.log('Plugin Folder doesn\'t exist. No plugins are loaded.')
      return
    }

    // Check for plugins in the folder
    const plugins = fs.readdirSync(pluginFolder)
      .filter(x => x.endsWith('.plugin.js'))
      .map(x => process.env.DEVELOPMENT ? path.join('www/hooks/plugins/', x) : path.join('hooks/plugins/', x))

    // Import them
    for (const plugin of plugins) {
      const el = document.createElement('script')
      el.type = 'text/javascript'
      if (process.env.DEVELOPMENT) {
        el.innerText = fs.readFileSync(plugin, 'utf-8')
      } else {
        el.src = plugin
      }

      const bodyEl = document.getElementsByTagName('body')
      bodyEl[0]?.appendChild(el)
    }
  }
}
