import { singleton } from 'tsyringe'
import { ISwitchData } from '../../sdk/lib/data/ISwitchData'
import { ISwitchManager } from '../../sdk/lib/ISwitchManager'

@singleton()
export class SwitchManager implements ISwitchManager {
  /**
   * Updates all switches
   * @param {boolean} value New switch value
   * @private
   */
  private _globalUpdate (value: boolean): void {
    for (const sw of this._list()) {
      $gameSwitches.setValue(sw.id, value)
    }
  }

  disableAll (): void {
    this._globalUpdate(false)
  }

  enableAll (): void {
    this._globalUpdate(true)
  }

  getSwitch (id: number): ISwitchData | undefined {
    const data = $gameSwitches.value(id)
    if (data === undefined || data === null) return

    return {
      id,
      name: $dataSystem.switches[id],
      value: data
    }
  }

  /**
   * Filters and returns list of switches
   * @private
   */
  private _list (): ISwitchData[] {
    return $dataSystem.switches
      .map((x, index) => ({
        id: index,
        name: x,
        value: $gameSwitches._data[index] ?? false
      }))
      .filter(x => x.name && x.name?.replace(/\s/g, '').length > 0)
  }

  get list (): ISwitchData[] {
    return this._list()
  }

  modify (id: number, newValue: boolean): void {
    $gameSwitches.setValue(id, newValue)
  }
}
