/* global IDataWeapon, IDataItem, IDataArmor */

export class InventoryData {
  private readonly _id: number
  private readonly _name: string

  constructor (data: IDataWeapon | IDataItem | IDataArmor) {
    this._name = data.name
    this._id = data.id
  }

  get id (): number {
    return this._id
  }

  get name (): string {
    return this._name
  }
}
