import React from 'react'
import { PersonIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'
import { PlayerCheatInterface } from './PlayerCheatInterface'

export class PlayerCheatPlugin extends Plugin<{}> {
  constructor () {
    super()
    this._pluginId = 'PlayerCheatPlugin'
    this._name = 'Player Cheat Plugin'
    this._pluginType = PluginTypes.Cheat
    this._author = 'Aiko Fujimoto'
  }

  getIcon (): React.ReactElement {
    return (<PersonIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<PlayerCheatInterface />)
  }
}
