import fs from 'fs'

/**
 * This namespace contains all wrappers commonly used fs modules to Promises.
 * Although fs-promises module exists, but the game's node version is old that it doesn't
 * have it unless the users will convert the engine to newer nw.js
 */
export namespace FileSystemPromises {
  export async function readFile (source: string, encoding: BufferEncoding): Promise<string> {
    return new Promise((resolve, reject) => {
      fs.readFile(source, encoding, (err, data) => {
        if (err) return reject(err)
        resolve(data)
      })
    })
  }

  export async function mkdir (target: string, options: fs.MakeDirectoryOptions): Promise<string | undefined> {
    return new Promise((resolve, reject) => {
      fs.mkdir(target, options, (err, path) => {
        if (err) return reject(err)
        resolve(path)
      })
    })
  }

  export async function writeFile (path: string, data: Buffer | string, options: fs.WriteFileOptions): Promise<void> {
    return new Promise((resolve, reject) => {
      fs.writeFile(path, data, options, (err) => {
        if (err) return reject(err)
        resolve()
      })
    })
  }
}
