import React from 'react'
import { CheckIcon } from '@primer/octicons-react'

export type AllowedFormTypes = 'text' | 'number'

interface SmallTypedInputComponentProps {
  baseId: string;
  name: string;
  type: AllowedFormTypes;
  readonly: boolean;
  value: string | number;
  handleOnChange: (value: string | number) => void;
  defaultDropdownValue: string;
  dropdownOptions: string[];
  handleDropdownSelect: (value: string) => void;
  handleSetAction: () => void;
}

export function SmallTypedInputComponent (props: SmallTypedInputComponentProps) {
  return (
    <div className="input-group input-group-sm mb-3">
      <span className="input-group-text" id={`${props.baseId}-label`}>
        {props.name}
      </span>
      <input
        type={props.type}
        className="form-control"
        placeholder={props.name}
        readOnly={props.readonly}
        value={props.value}
        onChange={(event: React.SyntheticEvent<HTMLInputElement>) => props.handleOnChange(event.currentTarget.value)}
        aria-label={props.name}
        aria-describedby={`${props.baseId}-label`}
      />
      <button
        className="btn btn-outline-secondary dropdown-toggle"
        type="button"
        data-bs-toggle="dropdown"
        aria-expanded="false">
        { props.defaultDropdownValue }
      </button>
      <ul className="dropdown-menu dropdown-menu-end">
        {
          props.dropdownOptions.map((x, index) => (
            <li key={index}>
              <a className="dropdown-item" onClick={() => props.handleDropdownSelect(x)}>
                {x}
              </a>
            </li>
          ))
        }
      </ul>
      <button
        className="btn btn-outline-success"
        type="button"
        onClick={props.handleSetAction}>
        <CheckIcon size={12} />
      </button>
    </div>
  )
}
