import React from 'react'
import { TypographyIcon } from '@primer/octicons-react'
import { Plugin } from '../../../sdk/Plugin'
import { TranslatorProp } from './TranslatorProp'
import { TranslatorInterface } from './TranslatorInterface'
import { PluginTypes } from '../../../sdk/enums/PluginTypes'

export class TranslatorPlugin extends Plugin {
  constructor () {
    super()
    this._pluginId = 'TranslatorPlugin'
    this._name = 'Translator Cheat Plugin'
    this._pluginType = PluginTypes.Cheat
    this._author = 'Aiko Fujimoto'
  }

  getIcon (): React.ReactElement {
    return (<TypographyIcon size={24} />)
  }

  getInterface (): React.ReactElement {
    return (<TranslatorInterface {...this._props as TranslatorProp} />)
  }
}
